<template>
  <div
    class="transform-toolbar"
    v-bind:style="[
      location == 'above'
        ? {
            top: 'calc(' + posTop + 'px - 140px)',
            left: 'calc(' + posLeft + 'px - 110px)',
          }
        : {
            top: 'calc(' + posTop + 'px + 100px',
            left: 'calc(' + posLeft + 'px - 110px)',
          },
    ]"
    v-bind:class="{ hide: !display }"
  >
    <div class="transform-modes">
      <span>
        <input
          type="radio"
          id="translate"
          name="transformations"
          value="translate"
          v-model="selectedTransformation"
          checked
        /><label for="translate"> Move</label>
      </span>
      <span>
        <input
          type="radio"
          id="rotate"
          name="transformations"
          value="rotate"
          v-model="selectedTransformation"
        /><label for="rotate"> Rotate</label>
      </span>
      <span>
        <input
          type="radio"
          id="scale"
          name="transformations"
          value="scale"
          v-model="selectedTransformation"
        /><label for="scale"> Scale</label>
      </span>
    </div>

    <div class="duplicate-controls" v-bind:class="{ hide: selectedTool == 'model' }">
        <button class="dup-btn" @click="duplicate">Duplicate</button>
        
        <div class="offset-group">
            <div class="inputs">
                <input v-model.number="offset.x" placeholder="X" type="number" step="0.1" />
                <input v-model.number="offset.y" placeholder="Y" type="number" step="0.1" />
                <input v-model.number="offset.z" placeholder="Z" type="number" step="0.1" />
            </div>
            <button class="dup-btn" @click="duplicateOffset">Offset Copy</button>
        </div>
    </div>
  </div>
</template>

<script>
import { select } from "./select.js";
import { scene, renderer, camera } from "../App.vue";
import * as THREE from 'three';

export default {
  name: "TransformToolbar",
  props: {
    top: Number,
    left: Number,
    location: String,
    display: Boolean,
    selectedTool: String,
  },
  data() {
    return {
      selectedTransformation: "translate",
      posTop: 0,
      posLeft: 0,
      offset: { x: 1, y: 0, z: 0 }
    };
  },
  methods: {
    duplicate: function () {
      select.s.duplicate();
    },
    duplicateOffset: function() {
        if(select.s) {
            select.s.duplicate(new THREE.Vector3(this.offset.x, this.offset.y, this.offset.z));
        }
    }
  },
  watch: {
    selectedTransformation: function (val) {
      if (this.selectedTool == "select" && select.s && select.s.controls) {
        select.s.controls.mode = val;
        //this maintains the selection from the last used transformation
        this.selectedTransformation = val;
        select.transformMode = val;
        select.s.helper.update();
      }
      if (this.selectedTool == "model") {
        let controls = scene.getObjectByName("canvasTransformControls");
        console.log(controls);
        this.selectedTransformation = val;
        if(controls) controls.mode = this.selectedTransformation;
      }
      renderer.render(scene, camera);
    },
    display: function (val) {
      if (val) {
        //this maintains the selection from the last used transformation
        if (this.selectedTool == "select" && select.s && select.s.controls) {
          select.s.controls.mode = this.selectedTransformation;
        }
        if (this.selectedTool == "model") {
          let controls = scene.getObjectByName("canvasTransformControls");
          if(controls) controls.mode = this.selectedTransformation;
        }
      }
    },
    top: function (val) {
      //this needs to be improved further

      if (val >= window.innerHeight) {
        this.posTop = window.innerHeight - 60;
      } else {
        this.posTop = val;
      }
    },
    left: function (val) {
      if (val >= window.innerWidth) {
        this.posLeft = window.innerWidth;
      } else {
        this.posLeft = val;
      }
    },
  },
  mounted() {},
};
</script>

<style scoped>
.transform-toolbar {
  padding: 8px;
  position: absolute;
  z-index: 999;
  background-color: rgba(30,30,30,0.9);
  color: white;
  width: 240px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  border: 1px solid rgba(255,255,255,0.1);
}

.transform-modes {
    display: flex;
    justify-content: space-around;
    padding-bottom: 8px;
    border-bottom: 1px solid rgba(255,255,255,0.1);
}

.transform-modes span {
    display: flex;
    align-items: center;
    gap: 4px;
}

.duplicate-controls {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.offset-group {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.inputs {
    display: flex;
    gap: 4px;
}

.inputs input {
    width: 100%;
    background: rgba(0,0,0,0.5);
    border: 1px solid rgba(255,255,255,0.2);
    color: white;
    padding: 2px 4px;
    border-radius: 4px;
    font-size: 12px;
}

.dup-btn {
    background: rgba(255,255,255,0.1);
    border: none;
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 12px;
    transition: background 0.2s;
}

.dup-btn:hover {
    background: rgba(255,255,255,0.2);
}

.hide {
  display: none;
}
</style>