import * as THREE from "three";
import { MeshLineRaycast } from "meshline";
import { scene } from "../App.vue";

let mirror = {
    updateMirrorOf: function (obj, activeScene, localCanvas) {
        if (obj.type === "Group") {
            obj.children.forEach((child) => {
                mirror.updateMirrorOf(child, activeScene, localCanvas);
            });
        } else if (obj.userData.mirrors && Array.isArray(obj.userData.mirrors)) {
            obj.userData.mirrors.forEach(mirrorData => {
                let mirroredObject = activeScene.getObjectByProperty(
                    "uuid",
                    mirrorData.uuid
                );
                if (!mirroredObject) return;

                const axis = mirrorData.axis;

                if (localCanvas) {
                    const m = localCanvas.matrixWorld;
                    const mInv = m.clone().invert();
                    let sx = 1, sy = 1, sz = 1;
                    if (axis === "x") sx = -1;
                    else if (axis === "y") sy = -1;
                    else if (axis === "z") sz = -1;
                    const mFlip = new THREE.Matrix4().makeScale(sx, sy, sz);
                    const transform = m.clone().multiply(mFlip).multiply(mInv);

                    mirroredObject.position.copy(obj.position);
                    mirroredObject.quaternion.copy(obj.quaternion);
                    mirroredObject.scale.copy(obj.scale);
                    mirroredObject.applyMatrix4(transform);
                } else {
                    var position = new THREE.Vector3();
                    obj.getWorldPosition(position);
                    var quaternion = new THREE.Quaternion();
                    obj.getWorldQuaternion(quaternion);
                    var scale = new THREE.Vector3();
                    obj.getWorldScale(scale);

                    switch (axis) {
                        case "x":
                            mirroredObject.position.set(-position.x, position.y, position.z);
                            mirroredObject.quaternion.set(-quaternion.x, quaternion.y, quaternion.z, -quaternion.w);
                            mirroredObject.scale.set(-scale.x, scale.y, scale.z);
                            break;
                        case "y":
                            mirroredObject.position.set(position.x, -position.y, position.z);
                            mirroredObject.quaternion.set(quaternion.x, -quaternion.y, quaternion.z, -quaternion.w);
                            mirroredObject.scale.set(scale.x, -scale.y, scale.z);
                            break;
                        case "z":
                            mirroredObject.position.set(position.x, position.y, -position.z);
                            mirroredObject.quaternion.set(quaternion.x, quaternion.y, -quaternion.z, -quaternion.w);
                            mirroredObject.scale.set(scale.x, scale.y, -scale.z);
                            break;
                    }
                }
                // Recursive update for nested mirrors (X then Y)
                mirror.updateMirrorOf(mirroredObject, activeScene, localCanvas);
            });
            obj.matrixWorldNeedsUpdate = true;
        }
    },
    object: function (obj, axis, activeScene, localCanvas) {
        var clone = obj.clone();
        clone.layers.set(1);
        clone.raycast = MeshLineRaycast;

        if (!obj.userData.mirrors) obj.userData.mirrors = [];
        obj.userData.mirrors.push({ uuid: clone.uuid, axis: axis });

        // The clone also needs to know it's a mirror and what axis it came from 
        // to support multi-axis update (A mirrors to B, then B mirrors to D)
        clone.userData.mirrorParent = obj.uuid;
        clone.userData.mirrorAxisFromParent = axis;

        if (localCanvas) {
            const m = localCanvas.matrixWorld;
            const mInv = m.clone().invert();
            let sx = 1, sy = 1, sz = 1;
            if (axis === "x") sx = -1;
            else if (axis === "y") sy = -1;
            else if (axis === "z") sz = -1;
            const mFlip = new THREE.Matrix4().makeScale(sx, sy, sz);
            const transform = m.clone().multiply(mFlip).multiply(mInv);
            clone.applyMatrix4(transform);
        } else {
            const mFlip = new THREE.Matrix4().makeScale(
                axis === "x" ? -1 : 1,
                axis === "y" ? -1 : 1,
                axis === "z" ? -1 : 1
            );
            clone.applyMatrix4(mFlip);
        }
        activeScene.add(clone);
        return clone;
    },
    eraseMirrorOf: function (obj) {
        if (obj.userData.mirrors) {
            obj.userData.mirrors.forEach(m => {
                let mirroredObject = scene.getObjectByProperty("uuid", m.uuid);
                if (mirroredObject) {
                    mirror.eraseMirrorOf(mirroredObject);
                    scene.remove(mirroredObject);
                }
            });
        }
    },
};

export { mirror };