<template>
  <draggable-window 
    title="Scene Setup" 
    :initialX="window.innerWidth - 240" 
    :initialY="400"
    :visible="visible"
    :reopenTrigger="reopenTrigger"
    :start-open="startOpen"
    :is-open="isOpen"
    @close="$emit('close')"
  >
    <div class="scene-settings">
      <!-- Background Settings -->
      <div class="group-title">Background</div>
      <div class="setting-row">
        <span>Color</span>
        <input type="color" v-model="backgroundColor" @input="updateBackground">
      </div>
       <div class="setting-row">
          <span>Fog</span>
          <div class="toggle" @click="toggleFog">
            <img :src="fogVisible ? require('@/assets/icons/checkbox-checked.svg') : require('@/assets/icons/checkbox.svg')" />
          </div>
      </div>

      <div class="separator"></div>

      <!-- Environment Settings -->
      <div class="group-title">Environment (HDRI)</div>
      <div class="setting-row">
        <span>Image (.hdr, .jpg)</span>
        <label class="custom-file-upload">
            <input type="file" @change="handleFileChange" accept=".hdr,.jpg,.png,.jpeg" />
            Upload
        </label>
      </div>
      <div class="setting-row">
        <span>Active (Lighting)</span>
        <div class="toggle" @click="toggleHdriActive">
          <img :src="hdriActive ? require('@/assets/icons/checkbox-checked.svg') : require('@/assets/icons/checkbox.svg')" />
        </div>
      </div>
       <div class="setting-row" v-if="hdriActive">
        <span>Visible Background</span>
        <div class="toggle" @click="toggleHdriBackground">
          <img :src="hdriBackgroundVisible ? require('@/assets/icons/checkbox-checked.svg') : require('@/assets/icons/checkbox.svg')" />
        </div>
      </div>

       <div class="separator"></div>




      <div class="separator"></div>

      <!-- Grid Settings -->
      <div class="group-title">Grid</div>
      <div class="setting-row">
        <span>Visible</span>
        <div class="toggle" @click="toggleGrid">
          <img :src="gridVisible ? require('@/assets/icons/checkbox-checked.svg') : require('@/assets/icons/checkbox.svg')" />
        </div>
      </div>
       <div class="setting-row" v-if="gridVisible">
        <span>Color</span>
        <input type="color" v-model="gridColor" @input="updateGrid">
      </div>
      <div class="setting-row" v-if="gridVisible">
        <span>Size</span>
        <input type="range" min="1" max="100" v-model.number="gridSize" @input="updateGrid">
      </div>
      <div class="setting-row" v-if="gridVisible">
        <span>Divisions</span>
        <input type="range" min="1" max="50" v-model.number="gridDivisions" @input="updateGrid">
      </div>
      <div class="setting-row" v-if="gridVisible">
        <span>Divisions Color</span>
        <input type="color" v-model="divisionsColor" @input="updateGrid">
      </div>
      <div class="setting-row">
        <span>Fade Dist</span>
        <input type="range" min="10" max="2000" v-model.number="fadeDistance" @input="updateGrid">
      </div>
      <div class="setting-row">
        <span>Fade Near</span>
        <input type="range" min="0" max="1000" v-model.number="fadeDistanceNear" @input="updateGrid">
      </div>

      <div class="setting-row">
          <span>Infinite</span>
          <div class="toggle" @click="toggleInfiniteGrid">
            <img :src="infiniteGrid ? require('@/assets/icons/checkbox-checked.svg') : require('@/assets/icons/checkbox.svg')" />
          </div>
      </div>

    </div>
  </draggable-window>
</template>

<script>
import DraggableWindow from "./DraggableWindow.vue";
import * as THREE from "three";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader.js";
import { scene, renderer, camera } from "../App.vue";
// import { canvas } from "./Canvas.vue";
import InfiniteGridHelper from "./InfiniteGridHelper.js";

export default {
  name: "SceneSettings",
  components: { DraggableWindow },
  props: {
    visible: Boolean,
    reopenTrigger: Number,
    startOpen: {
      type: Boolean,
      default: true,
    },
    isOpen: Boolean,
  },
  emits: ["close"],
  data() {
    return {
      window: window,
      backgroundColor: "#dddddd",
      gridVisible: true,
      gridColor: "#0000ff",
      gridSize: 10,
      gridDivisions: 10,
      infiniteGrid: true,
      fadeDistance: 400,
      fadeDistanceNear: 0,
      divisionsColor: "#ff0000",
      fogVisible: true,
      hdriActive: false,
      hdriBackgroundVisible: false,
      sceneEnvironment: null,
    };
  },
  created() {
    this.gridHelper = null;
    this.infiniteGridHelper = null;
  },
  methods: {
    updateBackground() {
      if (scene) {
        if (this.hdriActive && this.hdriBackgroundVisible && this.sceneEnvironment) {
             scene.background = this.sceneEnvironment;
        } else {
             scene.background = new THREE.Color(this.backgroundColor);
        }
        this.updateFog();
        renderer.render(scene, camera);
      }
    },
    updateFog() {
        if (!scene) return;
        if (this.fogVisible) {
            scene.fog = new THREE.Fog(this.backgroundColor, this.fadeDistanceNear + 10, this.fadeDistance);
        } else {
            scene.fog = null;
        }
    },
    toggleGrid() {
        this.gridVisible = !this.gridVisible;
        this.updateGrid();
    },
    toggleInfiniteGrid() {
        this.infiniteGrid = !this.infiniteGrid;
        this.updateGrid();
    },
    toggleFog() {
        this.fogVisible = !this.fogVisible;
        this.updateFog();
        renderer.render(scene, camera);
    },
    updateGrid() {
        if (!scene) return;

        this.updateFog(); // Update fog when grid settings (like fade distance) change

        // Remove existing grids
        if (this.gridHelper) {
            scene.remove(this.gridHelper);
            this.gridHelper = null;
        }
        if (this.infiniteGridHelper) {
            scene.remove(this.infiniteGridHelper);
            this.infiniteGridHelper = null;
        }

        if (!this.gridVisible) {
          renderer.render(scene, camera);
          return;
        }

        if (this.infiniteGrid) {
             // Pass divisionsColor as the second color (for large grid)
             this.infiniteGridHelper = new InfiniteGridHelper(
                 this.gridSize/10, 
                 this.gridDivisions, 
                 new THREE.Color(this.gridColor), 
                 this.fadeDistance,
                 'xzy',
                 new THREE.Color(this.divisionsColor),
                 this.fadeDistanceNear
             );
             scene.add(this.infiniteGridHelper);
        } else {
             this.gridHelper = new THREE.GridHelper(
                 this.gridSize * 10, 
                 this.gridDivisions * 10, 
                 new THREE.Color(this.divisionsColor), 
                 new THREE.Color(this.gridColor)
             );
             scene.add(this.gridHelper);
        }
        renderer.render(scene, camera);
    },


    handleFileChange(event) {
      const file = event.target.files[0];
      if (!file) return;

      const url = URL.createObjectURL(file);
      const isHDR = file.name.toLowerCase().endsWith('.hdr');

      if (isHDR) {
        new RGBELoader()
            .setDataType( THREE.UnsignedByteType ) // Depending on three version, might not be needed or might be FloatType
            .load(url, (texture) => {
                const pmremGenerator = new THREE.PMREMGenerator(renderer);
                pmremGenerator.compileEquirectangularShader();
                const envMap = pmremGenerator.fromEquirectangular(texture).texture;
                
                this.sceneEnvironment = envMap;
                this.hdriActive = true; 
                this.updateEnvironment();
                
                texture.dispose();
                pmremGenerator.dispose();
            });
      } else {
          new THREE.TextureLoader().load(url, (texture) => {
                texture.mapping = THREE.EquirectangularReflectionMapping;
                this.sceneEnvironment = texture;
                this.hdriActive = true;
                this.updateEnvironment();
          });
      }
    },
    toggleHdriActive() {
        this.hdriActive = !this.hdriActive;
        this.updateEnvironment();
    },
    toggleHdriBackground() {
        this.hdriBackgroundVisible = !this.hdriBackgroundVisible;
        this.updateBackground();
    },
    updateEnvironment() {
        if (!scene) return;

        if (this.hdriActive && this.sceneEnvironment) {
            scene.environment = this.sceneEnvironment;
        } else {
            scene.environment = null;
        }
        this.updateBackground();
    }
  },
  mounted() {
      // Initialize with existing scene state if possible, or defaults
      this.updateGrid();
  },
  beforeUnmount() {
      if (this.gridHelper) scene.remove(this.gridHelper);
      if (this.infiniteGridHelper) scene.remove(this.infiniteGridHelper);
  }
};
</script>

<style scoped>
.scene-settings {
  display: flex;
  flex-direction: column;
  gap: 12px;
  color: white;
  min-width: 200px;
}

.group-title {
  font-size: 10px;
  text-transform: uppercase;
  opacity: 0.6;
  font-weight: bold;
  letter-spacing: 1px;
}

.setting-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 13px;
}

.setting-row input[type="color"] {
  border: none;
  width: 24px;
  height: 24px;
  padding: 0;
  background: none;
  cursor: pointer;
}

.setting-row input[type="range"] {
  width: 100px;
  height: 4px;
  -webkit-appearance: none;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  outline: none;
}

.setting-row input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
}

.toggle {
    cursor: pointer;
    display: flex;
    align-items: center;
}

.toggle img {
    width: 20px;
    height: 20px;
    filter: brightness(0) invert(1);
}

.separator {
  height: 1px;
  background: rgba(255, 255, 255, 0.1);
  width: 100%;
}

.custom-file-upload {
    border: 1px solid rgba(255, 255, 255, 0.2);
    display: inline-block;
    padding: 2px 6px;
    cursor: pointer;
    border-radius: 4px;
    font-size: 11px;
    background: rgba(255, 255, 255, 0.05);
}

.custom-file-upload:hover {
    background: rgba(255, 255, 255, 0.15);
}

.custom-file-upload input[type="file"] {
    display: none;
}
</style>
