<template>
  <draggable-window 
    title="Scene Objects" 
    :initialX="12" 
    :initialY="300"
    :visible="visible"
    :reopenTrigger="reopenTrigger"
    :is-open="isOpen"
    @close="$emit('close')"
  >
    <div class="manager-container">
      <!-- Object List -->
      <div class="section-title">Add Shape</div>
      <div class="shapes-row">
        <button class="shape-add-btn" @click="addShape('plane')" title="Plane"></button>
        <button class="shape-add-btn" @click="addShape('cube')" title="Cube"></button>
        <button class="shape-add-btn" @click="addShape('sphere')" title="Sphere"></button>
        <button class="shape-add-btn" @click="addShape('cylinder')" title="Cylinder"></button>
      </div>

      <div class="separator"></div>

      <div class="section-title">Scene Objects</div>
      <div class="object-list">
        <div v-if="shapes.length === 0" class="empty-state">No objects</div>
        <div 
          v-for="(obj, index) in shapes" 
          :key="obj.uuid"
          class="object-item"
          :class="{ active: isSelected(obj) }"
          @click="selectShape(obj)"
        >
          <span class="object-name">{{ getObjectName(obj, index) }}</span>
          <span class="object-type">{{ getObjectType(obj) }}</span>
        </div>
      </div>

      <div class="separator"></div>

      <!-- Controls -->
      <div v-if="selectedObj" class="controls-section">
        <div class="section-title">Properties</div>
        
        <div class="control-row">
          <label>Color</label>
          <input type="color" class="color-picker" :value="getObjectColor(selectedObj)" @input="updateColor" />
        </div>
        
        <div class="control-row">
          <label>Transparency</label>
           <input type="range" min="0" max="100" :value="getObjectOpacity(selectedObj)" @input="updateOpacity" />
        </div>
        
        <div class="control-row">
           <button class="delete-obj-btn" @click="deleteSelectedObject">Delete Object</button>
        </div>

        <div class="control-row">
          <label>Texture</label>
          <label class="custom-file-upload">
            <input type="file" @change="handleImageUpload" accept=".jpg,.jpeg,.png,.webp" />
            <span> Load Image</span>
          </label>
        </div>

        <div class="separator"></div>

        <!-- History -->
        <div class="history-section">
            <div class="history-header">
                <span class="section-title">Position History</span>
                <button class="small-btn" @click="saveState">+ Save</button>
            </div>
            <div class="history-list">
                <div v-if="getHistory(selectedObj).length === 0" class="empty-state">No saved states</div>
                <div v-for="(state, sIndex) in getHistory(selectedObj)" :key="sIndex" class="history-item">
                    <span>{{ state.name }}</span>
                    <div class="actions">
                        <button class="icon-btn" @click="restoreState(state)" title="Restore"></button>
                        <button class="icon-btn delete" @click="deleteState(sIndex)" title="Delete"></button>
                    </div>
                </div>
            </div>
        </div>

      </div>
      <div v-else class="empty-selection">
        Select an object to edit properties
      </div>
    </div>
  </draggable-window>
</template>

<script>
import DraggableWindow from "./DraggableWindow.vue";
import * as THREE from "three";
import { shapes, canvas } from "./Canvas.vue"; // shapes is reactive
import { scene, renderer, camera, vm } from "../App.vue";

export default {
  name: "ObjectManager",
  components: { DraggableWindow },
  props: {
    visible: Boolean,
    reopenTrigger: Number,
    isOpen: Boolean,
  },
  emits: ["close"],
  computed: {
    shapes() {
        return shapes;
    },
    selectedObj() {
        // We trigger re-render by using canvas in computed? 
        // canvas is an exported let, not reactive. 
        // We relied on Canvas.vue to make shapes reactive. 
        // For selection state, we might need a way to know 'canvas' changed.
        // But clicking the list updates selection. 
        // We can track local selection or poll? 
        // Let's rely on user clicking our list to set 'currentSelection' locally for UI.
        return this.currentSelection;
    }
  },
  data() {
    return {
      currentSelection: null
    };
  },
  mounted() {
      // Try to sync with initial canvas
      this.currentSelection = canvas;
      // Optional: Polling or event listener to sync if selection changes from viewport
      this.timer = setInterval(() => {
          if (canvas !== this.currentSelection) {
              this.currentSelection = canvas;
          }
      }, 500);
  },
  beforeUnmount() {
      clearInterval(this.timer);
  },
  methods: {
    addShape(type) {
        if (vm && vm.$refs.raycastCanvas) {
            vm.$refs.raycastCanvas.addPrimitive(type);
        }
    },
    selectShape(obj) {
        this.currentSelection = obj;
        if (vm && vm.$refs.raycastCanvas) {
            vm.$refs.raycastCanvas.selectObject(obj);
        }
    },
    isSelected(obj) {
        return this.currentSelection === obj; // logic simplified
    },
    getObjectName(obj, index) {
        return obj.name || `Object ${index + 1}`;
    },
    getObjectType(obj) {
        if (obj.isGroup) return "Group/GLB";
        if (obj.geometry) return obj.geometry.type.replace("Geometry", "").replace("Buffer", "");
        return "Object";
    },
    getObjectMaterial(obj) {
        // Find first material
        let mat = null;
        if (obj.material) mat = obj.material;
        else {
            obj.traverse(child => {
                if (!mat && child.isMesh && child.material) mat = child.material;
            });
        }
        return mat;
    },
    getObjectColor(obj) {
        const mat = this.getObjectMaterial(obj);
        if (mat && mat.color) {
            return "#" + mat.color.getHexString();
        }
        return "#ffffff";
    },
    updateColor(event) {
        const color = event.target.value;
        const obj = this.selectedObj;
        if (!obj) return;
        
        const mat = this.getObjectMaterial(obj);
        if (mat) {
             // If array of materials?
            if (Array.isArray(mat)) {
                mat.forEach(m => m.color.set(color));
            } else {
                mat.color.set(color);
            }
            renderer.render(scene, camera);
        } else if (obj.isGroup) {
             // traverse and set all
             obj.traverse(child => {
                 if (child.isMesh && child.material) {
                     child.material.color.set(color);
                 }
             });
             renderer.render(scene, camera);
        }
    },
    getObjectOpacity(obj) {
        const mat = this.getObjectMaterial(obj);
        if (mat) {
             return (mat.opacity !== undefined ? mat.opacity : 1) * 100;
        }
        return 100;
    },
    updateOpacity(event) {
        const opacity = parseFloat(event.target.value) / 100;
        const obj = this.selectedObj;
        if (!obj) return;
        
        const updateMatOpacity = (m) => {
            m.opacity = opacity;
            m.transparent = opacity < 1;
            m.needsUpdate = true;
        };

        const mat = this.getObjectMaterial(obj);
        if (mat) {
            if (Array.isArray(mat)) {
                mat.forEach(m => updateMatOpacity(m));
            } else {
                updateMatOpacity(mat);
            }
        } else if (obj.isGroup) {
             obj.traverse(child => {
                 if (child.isMesh && child.material) {
                      updateMatOpacity(child.material);
                 }
             });
        }
        renderer.render(scene, camera);
    },
    deleteSelectedObject() {
        const obj = this.selectedObj;
        if (!obj) return;
        
        // Remove from scene
        if (obj.parent) {
            obj.parent.remove(obj);
        }
        
        // Remove from shapes array
        const index = shapes.indexOf(obj);
        if (index > -1) {
            shapes.splice(index, 1);
        }
        
        // Clear selection
        this.currentSelection = null;
        if (vm && vm.$refs.raycastCanvas) {
             vm.$refs.raycastCanvas.selectObject(null);
        }
        
         renderer.render(scene, camera);
    },
    handleImageUpload(event) {
         const file = event.target.files[0];
        if (!file || !this.selectedObj) return;

        const url = URL.createObjectURL(file);
        new THREE.TextureLoader().load(url, (texture) => {
            const obj = this.selectedObj;
            if (obj.isGroup) {
                 obj.traverse(child => {
                     if (child.isMesh && child.material) {
                         child.material.map = texture;
                         child.material.needsUpdate = true;
                         // Reset color to white so texture shows
                         child.material.color.set(0xffffff);
                     }
                 });
            } else if (obj.material) {
                obj.material.map = texture;
                obj.material.needsUpdate = true;
                obj.material.color.set(0xffffff);
            }
            renderer.render(scene, camera);
        });
    },
    getHistory(obj) {
        if (!obj) return [];
        if (!obj.userData.history) obj.userData.history = [];
        return obj.userData.history;
    },
    saveState() {
        const obj = this.selectedObj;
        if (!obj) return;
        
        const history = this.getHistory(obj);
        const state = {
            name: `State ${history.length + 1}`,
            position: obj.position.clone(),
            quaternion: obj.quaternion.clone(),
            scale: obj.scale.clone()
        };
        history.push(state);
    },
    restoreState(state) {
        const obj = this.selectedObj;
        if (!obj) return;
        
        obj.position.copy(state.position);
        obj.quaternion.copy(state.quaternion);
        obj.scale.copy(state.scale);
        renderer.render(scene, camera);
    },
    deleteState(index) {
        const obj = this.selectedObj;
        if (!obj) return;
        obj.userData.history.splice(index, 1);
    }
  }
};
</script>

<style scoped>
.manager-container {
  background: rgba(30, 30, 30, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 8px;
  padding: 10px;
  min-width: 250px;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: 80vh; 
  overflow: hidden;
}

.section-title {
    font-size: 11px;
    text-transform: uppercase;
    opacity: 0.6;
    font-weight: 800;
    letter-spacing: 0.5px;
    margin-bottom: 4px;
}


.shapes-row {
    display: flex;
    gap: 8px;
    justify-content: space-around;
}

.shape-add-btn {
    background: rgba(255,255,255,0.1);
    border: 1px solid rgba(255,255,255,0.2);
    border-radius: 4px;
    width: 32px;
    height: 32px;
    cursor: pointer;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;
}

.shape-add-btn:hover {
    background: rgba(255,255,255,0.2);
    transform: scale(1.1);
}

.object-list {
    display: flex;
    flex-direction: column;
    gap: 4px;
    max-height: 150px;
    overflow-y: auto;
}

.object-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 8px;
    background: rgba(255,255,255,0.05);
    border-radius: 4px;
    cursor: pointer;
    font-size: 13px;
    border: 1px solid transparent;
}

.object-item:hover {
    background: rgba(255,255,255,0.1);
}

.object-item.active {
    background: rgba(255, 176, 0, 0.2);
    border-color: rgba(255, 176, 0, 0.5);
    color: #ffb000;
}

.object-type {
    font-size: 10px;
    opacity: 0.5;
    background: rgba(0,0,0,0.2);
    padding: 2px 4px;
    border-radius: 4px;
}

.separator {
    height: 1px;
    background: rgba(255,255,255,0.1);
    width: 100%;
}

.controls-section {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.control-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 13px;
}

.color-picker {
    width: 40px;
    height: 24px;
    border: none;
    padding: 0;
    background: none;
    cursor: pointer;
}

.custom-file-upload {
    border: 1px solid rgba(255,255,255,0.2);
    border-radius: 4px;
    padding: 4px 8px;
    font-size: 11px;
    cursor: pointer;
    background: rgba(255,255,255,0.05);
}

.delete-obj-btn {
    width: 100%;
    padding: 6px;
    background: rgba(255, 50, 50, 0.2);
    border: 1px solid rgba(255, 50, 50, 0.4);
    color: #ffcccc;
    border-radius: 4px;
    cursor: pointer;
    font-size: 12px;
    margin-top: 8px;
    transition: all 0.2s;
}

.delete-obj-btn:hover {
    background: rgba(255, 50, 50, 0.4);
}
.custom-file-upload input {
    display: none;
}
.custom-file-upload:hover {
    background: rgba(255,255,255,0.1);
}

.empty-state, .empty-selection {
    text-align: center;
    opacity: 0.5;
    font-size: 12px;
    padding: 10px;
}

.history-section {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.history-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.small-btn {
    font-size: 10px;
    padding: 2px 6px;
    background: #ffb000;
    color: black;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
}

.history-list {
    max-height: 120px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.history-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 8px;
    background: rgba(0,0,0,0.2);
    border-radius: 4px;
    font-size: 12px;
}

.actions {
    display: flex;
    gap: 4px;
}

.icon-btn {
    background: transparent;
    border: 1px solid rgba(255,255,255,0.1);
    color: white;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    cursor: pointer;
    font-size: 10px;
}
.icon-btn:hover {
    background: rgba(255,255,255,0.1);
}
.icon-btn.delete:hover {
    background: rgba(255,50,50,0.3);
}

/* Scrollbars */
.object-list::-webkit-scrollbar, .history-list::-webkit-scrollbar {
    width: 4px;
}
.object-list::-webkit-scrollbar-thumb, .history-list::-webkit-scrollbar-thumb {
    background: rgba(255,255,255,0.2);
    border-radius: 2px;
}
</style>
