<template>
  <draggable-window ref="win" title="Tools" :initialX="initialX || (window.innerWidth / 2 - 300)" :initialY="initialY || 12" :reopenTrigger="reopenTrigger" :is-open="isOpen" @close="$emit('close')">
    <div class="tools-container">
      <!-- Drawing Tools -->
      <div class="tool-group">
        <button 
          class="tool-btn"
          :class="{ active: tool === 'draw' }"
          @click="setTool('draw', true)"
          :title="'Draw'"
        >
          <img src="@/assets/icons/draw.svg" alt="Draw" />
          <span class="tooltip">Draw</span>
        </button>
        
        <button 
          class="tool-btn"
          :class="{ active: tool === 'erase' }"
          @click="setTool('erase', true)"
          :title="'Erase'"
        >
          <img src="@/assets/icons/erase.svg" alt="Erase" />
          <span class="tooltip">Erase</span>
        </button>
      </div>

      <div class="separator"></div>

      <!-- Brush Presets -->
      <div class="tool-group brushes">
        <button 
          v-for="brush in brushes" 
          :key="brush.name"
          class="tool-btn brush-btn"
          @click="selectPreset(brush)"
          :title="brush.name"
        >
          <span class="brush-emoji">{{ brush.icon }}</span>
          <span class="tooltip">{{ brush.name }}</span>
        </button>
      </div>

      <div class="separator"></div>

      <!-- Properties Toggle -->
      <button 
        class="tool-btn"
        @click="toggleProperties"
        title="Properties"
      >
        <span class="properties-icon"></span>
        <span class="tooltip">Properties</span>
      </button>

      <!-- Scene Toggle -->
      <button 
        class="tool-btn"
        @click="toggleScene"
        title="Scene"
      >
        <span class="properties-icon"></span>
        <span class="tooltip">Scene</span>
      </button>

      <!-- 2D Shapes Toggle -->
      <button 
        class="tool-btn"
        @click="$emit('toggle-2d-shapes')"
        title="2D Shapes"
      >
        <span class="properties-icon"></span>
        <span class="tooltip">Shapes</span>
      </button>
    </div>
  </draggable-window>
</template>

<script>
import DraggableWindow from "./DraggableWindow.vue";

export default {
  name: "ToolSelector",
  components: { DraggableWindow },
  data() {
    return {
      tool: undefined,
      window: window,
      brushes: [
        { 
          name: "Pencil", 
          icon: "", 
          settings: { strokeWidth: 2, strokeColor: { r: 50, g: 50, b: 50 }, stroke: true, fill: false }
        },
        { 
          name: "Pen", 
          icon: "", 
          settings: { strokeWidth: 5, strokeColor: { r: 0, g: 0, b: 0 }, stroke: true, fill: false }
        },
        { 
          name: "Marker", 
          icon: "", 
          settings: { strokeWidth: 20, strokeColor: { r: 255, g: 0, b: 0 }, stroke: true, fill: false }
        },
        { 
          name: "Dashed Pencil", 
          icon: "", 
          settings: { 
            strokeWidth: 2, 
            strokeColor: { r: 50, g: 50, b: 50 }, 
            stroke: true, 
            fill: false,
            dashArray: 0.1,
            dashRatio: 0.5,
          }
        },
        { 
          name: "Highlighter", 
          icon: "", 
          settings: { 
            strokeWidth: 30, 
            strokeColor: { r: 255, g: 255, b: 0 }, 
            stroke: true, 
            fill: false,
            opacity: 0.3,
            transparent: true,
            depthTest: false,
            alphaTest: 0.01,
          }
        },
        { 
          name: "Ghost Ink", 
          icon: "", 
          settings: { 
            strokeWidth: 4, 
            strokeColor: { r: 200, g: 200, b: 255 }, 
            stroke: true, 
            fill: false,
            opacity: 0.3,
            transparent: true,
            alphaTest: 0.0,
          }
        },
        { 
          name: "Cinta", 
          icon: "", 
          settings: { 
            strokeWidth: 2, 
            strokeColor: { r: 255, g: 0, b: 100 }, 
            fillColor: { r: 255, g: 100, b: 150 },
            stroke: true, 
            fill: true,
          }
        }
      ]
    };
  },
  props: {
    selectedTool: String,
    reopenTrigger: Number,
    isOpen: Boolean,
    initialX: Number,
    initialY: Number,
  },
  emits: ["selected-tool", "set-preset", "toggle-properties", "toggle-scene", "toggle-2d-shapes", "close"],
  watch: {
    selectedTool: function (val) {
      this.tool = val;
    },
  },
  methods: {
    getPosition() {
       return { 
         x: this.$refs.win.currentX, 
         y: this.$refs.win.currentY 
       };
    },
    setTool: function (val, emit) {
      this.tool = val;
      if (emit) {
        this.$emit("selected-tool", val);
      }
    },
    selectPreset: function(brush) {
      // Ensure we're in draw mode when selecting a brush
      this.setTool('draw', true);
      this.$emit("set-preset", brush.settings);
    },
    toggleProperties: function() {
      this.$emit("toggle-properties");
    },
    toggleScene: function() {
      this.$emit("toggle-scene");
    },
  },
  mounted() {
    this.setTool(this.selectedTool);
  },
};
</script>

