<template>
  <draggable-window 
    title="Properties" 
    :initialX="12" 
    :initialY="300"
    :visible="forceVisible || selectedTool == 'draw'"
    :reopenTrigger="reopenTrigger"
    :is-open="isOpen"
    @close="$emit('close')"
  >
      <div class="panel">
        <line-sliders
          mode="stroke"
          :active="stroke"
          @color="setStrokeColor"
          @active="setStrokeActive"
          @width="setStrokeWidth"
        />
        
        <div v-if="stroke" class="extra-settings">
          <div class="section-title">Brush Settings</div>
          <!-- Shape -->
          <div class="setting-row">
            <span>Shape</span>
            <div class="ratio-buttons">
              <button class="ratio-btn" :class="{ active: brushType === 'solid' }" @click="brushType = 'solid'">Solid</button>
              <button class="ratio-btn" :class="{ active: brushType === 'soft' }" @click="brushType = 'soft'">Soft</button>
              <button class="ratio-btn" :class="{ active: brushType === 'noise' }" @click="brushType = 'noise'">Sketch</button>
            </div>
          </div>



          <!-- Taper -->
           <div class="setting-row">
            <span>Taper</span>
            <input type="range" min="0" max="20" step="1" v-model.number="taper" />
            <span class="value">{{ taper }}</span>
          </div>

          <!-- Smoothing -->
           <div class="setting-row">
            <span>Smooth</span>
            <input type="range" min="1" max="20" step="1" v-model.number="smoothing" />
            <span class="value">{{ smoothing }}</span>
          </div>

          <!-- Texture Scale -->
          <div class="setting-row" v-if="brushType !== 'solid' || brushMaterial !== 'standard'">
            <span>Tex Scale</span>
            <input type="range" min="0.1" max="5" step="0.1" v-model.number="textureScale" />
            <span class="value">{{ textureScale }}</span>
          </div>

          <!-- Wiggle -->
           <div class="setting-row">
            <span>Wiggle</span>
            <input type="range" min="0" max="0.5" step="0.01" v-model.number="wiggle" />
            <span class="value">{{ Math.round(wiggle * 100) }}</span>
          </div>

          <div class="section-title" style="margin-top: 8px;">Line Settings</div>

          <!-- Opacity -->
          <div class="setting-row">
            <span>Opacity</span>
            <input type="range" min="0" max="1" step="0.01" v-model.number="opacity" />
            <span class="value">{{ Math.round(opacity * 100) }}%</span>
          </div>

          <!-- Dash Size -->
          <div class="setting-row">
            <span>Dash Size</span>
            <input type="range" min="0" max="1" step="0.01" v-model.number="dashArray" />
          </div>

          <!-- Dash Ratio -->
          <div class="setting-row">
             <span>Dash Scale</span>
             <div class="ratio-buttons">
                <button class="ratio-btn" :class="{ active: Math.abs(dashRatio - 0.66) < 0.1 }" @click="dashRatio = 0.66">2:1</button>
                <button class="ratio-btn" :class="{ active: Math.abs(dashRatio - 0.5) < 0.1 }" @click="dashRatio = 0.5">1:1</button>
                <button class="ratio-btn" :class="{ active: Math.abs(dashRatio - 0.33) < 0.1 }" @click="dashRatio = 0.33">1:2</button>
             </div>
          </div>

          <div class="section-title" style="margin-top: 8px;">Animation</div>

          <!-- Animation Speed -->
          <div class="setting-row">
            <span>Speed</span>
            <input type="range" min="0" max="2" step="0.01" v-model.number="animationSpeed" />
            <span class="value">{{ Math.round(animationSpeed * 100) }}</span>
          </div>
        </div>

        <line-sliders
          :active="fill"
          mode="fill"
          @color="setFillColor"
          @active="setFillActive"
        />

        <div v-if="fill" class="extra-settings">
          <div class="setting-row">
            <span>Opacity</span>
            <input type="range" min="0" max="1" step="0.01" v-model.number="fillOpacity" />
            <span class="value">{{ Math.round(fillOpacity * 100) }}%</span>
          </div>
        </div>
      </div>
  </draggable-window>
</template>

<script>
import LineSliders from "./LineSliders.vue";
// import LineWidthSelector from "./LineWidthSelector.vue";

import DraggableWindow from "./DraggableWindow.vue";

export default {
  components: { LineSliders, DraggableWindow },
  name: "Modal",
  data() {
    return {
      window: window,
      stroke: true,
      strokeColor: "#1C1C1E",
      strokeWidth: 5,
      fill: false,
      fillColor: "#000000",
      // panelVisible: false, // Removed
      dashArray: 0,
      dashOffset: 0,
      dashRatio: 0.5,
      opacity: 1,
      fillOpacity: 1,
      transparent: false,
      alphaTest: 0.5,
      brushType: 'solid',
      brushMaterial: 'standard',
      taper: 3,
      smoothing: 4,
      textureScale: 1,
      wiggle: 0,
      animationSpeed: 0,
    };
  },
  props: {
    selectedTool: String,
    forceVisible: Boolean,
    reopenTrigger: Number,
    isOpen: Boolean,
  },
  emits: ["stroke", "fill", "close"],   
  methods: {
    setStrokeColor: function (val) {
      this.strokeColor = "rgb(" + val.r + "," + val.g + "," + val.b + ")";
    },
    setStrokeActive: function (val) {
      this.stroke = val.active;
    },
    setStrokeWidth: function (val) {
      this.strokeWidth = val.width;
    },
    setFillColor: function (val) {
      this.fillColor = "rgb(" + val.r + "," + val.g + "," + val.b + ")";
    },
    setFillActive: function (val) {
      this.fill = val.active;
    },
    emitStroke: function () {
      this.$emit("stroke", {
        show_stroke: this.stroke,
        lineWidth: this.strokeWidth / 500,
        color: this.strokeColor,
        dashArray: this.dashArray,
        dashOffset: this.dashOffset,
        dashRatio: this.dashRatio,
        opacity: this.opacity,
        transparent: this.transparent,
        alphaTest: this.alphaTest,
        brushType: this.brushType,
        brushMaterial: this.brushMaterial,
        taper: this.taper,
        smoothing: this.smoothing,
        textureScale: this.textureScale,
        wiggle: this.wiggle,
        animationSpeed: this.animationSpeed,
      });
    },
    emitFill: function () {
      this.$emit("fill", { show_fill: this.fill, color: this.fillColor, opacity: this.fillOpacity });
    },
    updateSettings: function (settings) {
      if (settings.stroke !== undefined) this.stroke = settings.stroke;
      if (settings.strokeColor !== undefined) {
         this.strokeColor = "rgb(" + settings.strokeColor.r + "," + settings.strokeColor.g + "," + settings.strokeColor.b + ")";
      }
      if (settings.strokeWidth !== undefined) this.strokeWidth = settings.strokeWidth;
      
      this.dashArray = settings.dashArray !== undefined ? settings.dashArray : 0;
      this.dashOffset = settings.dashOffset !== undefined ? settings.dashOffset : 0;
      this.dashRatio = settings.dashRatio !== undefined ? settings.dashRatio : 0.5;
      this.opacity = settings.opacity !== undefined ? settings.opacity : 1;
      this.transparent = settings.transparent !== undefined ? settings.transparent : false;
      this.alphaTest = settings.alphaTest !== undefined ? settings.alphaTest : 0.5;

      if (settings.brushType !== undefined) this.brushType = settings.brushType;
      if (settings.brushMaterial !== undefined) this.brushMaterial = settings.brushMaterial;
      if (settings.taper !== undefined) this.taper = settings.taper;
      if (settings.smoothing !== undefined) this.smoothing = settings.smoothing;
      if (settings.textureScale !== undefined) this.textureScale = settings.textureScale;
      if (settings.wiggle !== undefined) this.wiggle = settings.wiggle;
      if (settings.animationSpeed !== undefined) this.animationSpeed = settings.animationSpeed;

      if (settings.fill !== undefined) this.fill = settings.fill;
      if (settings.fillColor !== undefined) {
         this.fillColor = "rgb(" + settings.fillColor.r + "," + settings.fillColor.g + "," + settings.fillColor.b + ")";
      }
      if (settings.fillOpacity !== undefined) this.fillOpacity = settings.fillOpacity;
    },
  },
  watch: {
    stroke: function () {
      this.emitStroke();
    },
    strokeWidth: function () {
      this.emitStroke();
    },
    strokeColor: function () {
      this.emitStroke();
    },
    fill: function () {
      this.emitFill();
    },
    fillColor: function () {
      this.emitFill();
    },
    fillOpacity: function() {
      this.emitFill();
    },
    dashArray: function () {
      this.emitStroke();
    },
    dashRatio: function () {
      this.emitStroke();
    },
    opacity: function () {
      this.emitStroke();
    },
    brushType: function() {
      this.emitStroke();
    },
    taper: function() {
      this.emitStroke();
    },
    smoothing: function() {
      this.emitStroke();
    },
    textureScale: function() {
      this.emitStroke();
    },
    wiggle: function() {
      this.emitStroke();
    },
    animationSpeed: function() {
      this.emitStroke();
    },
  },
  mounted() {
    this.emitStroke();
    this.emitFill();
  },
};
</script>

<style>
.panel {
  display: flex;
  flex-direction: column;
  gap: 8px;
  /* background-color: white; Remove bg since window has glass */
  /* filter: drop-shadow(0px 0px 24px rgba(0, 0, 0, 0.08)); */
  width: fit-content;
  /* padding: 8px; */
  /* border-radius: 8px; */
}

.extra-settings {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0 8px 0 44px; /* Align with sliders roughly */
}

.setting-row {
  display: flex;
  align-items: center;
  gap: 8px;
  color: white;
  font-size: 12px;
  font-family: 'Inter', sans-serif;
}

.setting-row > span:first-child {
  width: 70px;
  opacity: 0.8;
}

.setting-row input[type="range"] {
  height: 4px;
  -webkit-appearance: none;
  appearance: none;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  flex: 1;
  outline: none;
}

.setting-row input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
  border: 2px solid rgba(0,0,0,0.5);
}

.setting-row .value {
  width: 32px;
  text-align: right;
  font-family: monospace;
}

.ratio-buttons {
  display: flex;
  gap: 4px;
}

.ratio-btn {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  border-radius: 4px;
  padding: 4px 8px;
  cursor: pointer;
  font-size: 10px;
  transition: all 0.2s;
}

.ratio-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.ratio-btn.active {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.6);
  font-weight: bold;
}

.section-title {
  color: white;
  font-size: 10px;
  font-weight: bold;
  opacity: 0.5;
  text-transform: uppercase;
  margin-bottom: 4px;
}
</style>