<template>
  <button @click="$emit('click')">
    <span class="label">{{ label }}</span>
    <span class="check" v-if="checked"></span>
  </button>
</template>

<script>
export default {
  name: "ToggleWindow",
  props: {
    label: {
      type: String,
      required: true,
    },
    checked: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["click"],
};
</script>

<style scoped>
button {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  align-content: center;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  height: 44px;
  padding-left: 12px;
  padding-right: 12px;
  border-bottom: 1px solid #ffe8b3;
  font-size: 0.8em;
  text-align: left;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

button:hover {
  background-color: rgba(255, 232, 179, 0.1);
}

.check {
    font-weight: bold;
    color: #ffb000;
}
</style>
