<template>
  <draggable-window 
    ref="win"
    title="Canvas" 
    :initialX="initialX || 12" 
    :initialY="initialY || 12"
    :visible="selectedTool == 'draw'"
    :reopenTrigger="reopenTrigger"
    :is-open="isOpen"
    @close="$emit('close')"
  >
    <div class="canvas-controls">
      <!-- Transform Mode -->
      <button 
        class="control-btn"
        @click="toggleTransformMode()"
        :disabled="!visible"
        :title="mode === 'combined' ? 'Move Mode' : 'Scale Mode'"
      >
        <img v-if="mode === 'combined' ? 'Move Mode' : 'Scale Mode'" src="@/assets/icons/translate.svg" alt="Move" />
        <img v-if="mode === 'scale'" src="@/assets/icons/scale.svg" alt="Scale" />
        <span class="tooltip">{{ mode === 'combined' ? 'Move Mode' : 'Scale Mode' }}</span>
      </button>

      <!-- Lock/Unlock -->
      <button 
        class="control-btn"
        :class="{ active: !transformationEnabled }"
        @click="toggleControls()"
        :disabled="!visible"
        :title="transformationEnabled ? 'Unlocked' : 'Locked'"
      >
        <img 
          :src="transformationEnabled ? require('@/assets/icons/lockControls.svg') : require('@/assets/icons/unlockControls.svg')" 
          alt="Lock"
        />
        <span class="tooltip">{{ transformationEnabled ? 'Unlocked' : 'Locked' }}</span>
      </button>

      <!-- Visible/Hidden -->
      <button 
        class="control-btn"
        :class="{ active: !visible }"
        @click="toggleVisibility()"
        :title="visible ? 'Visible' : 'Hidden'"
      >
        <img 
          :src="visible ? require('@/assets/icons/hideCanvas.svg') : require('@/assets/icons/showCanvas.svg')" 
          alt="Visibility"
        />
        <span class="tooltip">{{ visible ? 'Visible' : 'Hidden' }}</span>
      </button>

      <!-- Snap -->
      <button 
        class="control-btn"
        :class="{ active: snap }"
        @click="toggleSnap()"
        :disabled="!visible"
        :title="snap ? 'Snap On' : 'Snap Off'"
      >
        <img 
          :src="snap ? require('@/assets/icons/snapOn.svg') : require('@/assets/icons/snapOff.svg')" 
          alt="Snap"
        />
        <span class="tooltip">{{ snap ? 'Snap On' : 'Snap Off' }}</span>
      </button>

      <!-- Reset -->
      <button 
        class="control-btn"
        @click="resetTransformation()"
        :disabled="transformationResetDisabled || !visible"
        title="Reset Canvas"
      >
        <img src="@/assets/icons/reset.svg" alt="Reset" />
        <span class="tooltip">Reset Canvas</span>
      </button>
    </div>
  </draggable-window>
</template>

<script>
import { reactive } from "vue";
import * as THREE from "three";
import { TransformControls } from "./transformControls.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { scene, renderer, camera, vm } from "../App.vue";
import DraggableWindow from "./DraggableWindow.vue";
// import { generateMatcap } from "./matcaps.js";

    export let canvas, controls, currentShape;
    export let shapes = reactive([]); // List of all shapes managed by this component

    let canvasMirror;
    let geometry = new THREE.PlaneGeometry(5, 5);
    let headGeometry;

    export default {
    name: "Canvas",
    components: { DraggableWindow },
    data() {
        return {
        window: window,
        material: new THREE.MeshToonMaterial({
            color: 0xe6edf5,
            transparent: true,
            opacity: 0.8,
            side: THREE.DoubleSide,
            polygonOffset: true,
            polygonOffsetFactor: 2.5,
            polygonOffsetUnits: -1,
            emissive: new THREE.Color("rgb(255,255,255)"),
            emissiveIntensity: 0.3,
        }),
        startPosition: new THREE.Vector3(0.001, 0.001, 0.001),
        startQuaternion: new THREE.Quaternion(0.001, 0.001, 0.001, 1),
        startScale: new THREE.Vector3(1, 1, 1),
        transformationResetDisabled: true,
        transformationEnabled: true,
        visible: true,
        mode: "combined",
        snap: false,
        };
    },
    props: {
        selectedShape: [String, Object], // Allow object for custom model selection
        selectedTool: String,
        mirror: [Boolean, String],
        mouse: Object,
        reopenTrigger: Number,
        isOpen: Boolean,
        initialX: Number,
        initialY: Number,
    },
    emits: ["selected-canvas-shape", "set-tool-enabled", "visibility-change", "close"],
    methods: {
        getPosition() {
        return { 
            x: this.$refs.win.currentX, 
            y: this.$refs.win.currentY 
        };
        },
        loadCustomModel(payload) {
        let url, id;
        if (typeof payload === 'object' && payload.url) {
             url = payload.url;
             id = payload.id;
        } else {
             url = payload;
        }

        if (!url) return;
        const loader = new GLTFLoader();
        loader.load(url, (gltf) => {
            const model = gltf.scene;
            
            if (id) {
                model.userData.customId = id;
                model.name = "Custom Model " + id.substr(-4);
            } else {
                model.name = "Custom Model";
            }

            const box = new THREE.Box3().setFromObject(model);
            const center = box.getCenter(new THREE.Vector3());
            model.position.sub(center); 
            
            scene.add(model);
            shapes.push(model);
            
            this.selectObject(model);
            renderer.render(scene, camera);
        });
        },
        addPrimitive(type) {
            let geo;
            const mat = this.material.clone(); // Independent material
            mat.visible = true;
            mat.opacity = 0.8; 

            switch (type) {
                case "plane":
                    geo = new THREE.PlaneGeometry(5, 5);
                    break;
                case "cube":
                    geo = new THREE.BoxGeometry(5, 5, 5);
                    break;
                case "cylinder":
                    geo = new THREE.CylinderGeometry(2.5, 2.5, 5, 15);
                    break;
                case "sphere":
                    geo = new THREE.SphereGeometry(2.5, 15, 15);
                    break;
                default:
                    return;
            }

            const mesh = new THREE.Mesh(geo, mat);
            mesh.name = type.charAt(0).toUpperCase() + type.slice(1);
            
            scene.add(mesh);
            shapes.push(mesh);
            this.selectObject(mesh);
        },
        addShape(mesh) {
            // Apply Canvas Material
            const mat = this.material.clone();
            mat.visible = true;
            mat.opacity = 0.8;
            mesh.material = mat;
            
            // Ensure unique name
            if (!mesh.name) mesh.name = "Shape " + Date.now();
            
            scene.add(mesh);
            shapes.push(mesh);
            this.selectObject(mesh);
            renderer.render(scene, camera);
        },
        selectObject(object) {
            if (!object) return;
            
            // Update the exported 'canvas' reference
            // Note: Since 'canvas' is a 'let', we can reassign it.
            // Check if controls are already attached to this object
            if (controls.object === object) return;

            controls.detach();
            canvas = object;
            controls.attach(canvas);
            
            // Update UI state if needed (e.g., reset button)
            this.transformationResetDisabled = false; // Assume loaded models might be transformed eventually
            
            renderer.render(scene, camera);
        },
        setUp() {
        const material = this.material;
        canvas = new THREE.Mesh(geometry, material);
        scene.add(canvas);
        shapes.push(canvas);

        controls = new TransformControls(camera, renderer.domElement);
        controls.mode = "combined";
        controls.addEventListener("change", () => {
            if (vm != undefined) {
            vm.$refs.raycastCanvas.transformationResetDisabled = false;
            }

            if (canvasMirror !== undefined) {
            var position = new THREE.Vector3();
            canvas.getWorldPosition(position);
            var quaternion = new THREE.Quaternion();
            canvas.getWorldQuaternion(quaternion);
            var scale = new THREE.Vector3();
            canvas.getWorldScale(scale);
            switch (vm.$refs.raycastCanvas.mirror) {
                case "x":
                canvasMirror.position.set(-position.x, position.y, position.z);
                canvasMirror.quaternion.set(
                    -quaternion.x,
                    quaternion.y,
                    quaternion.z,
                    -quaternion.w
                );
                canvasMirror.scale.set(-scale.x, scale.y, scale.z);
                canvasMirror.matrixWorldNeedsUpdate = true;
                break;
                case "y":
                canvasMirror.position.set(position.x, -position.y, position.z);
                canvasMirror.quaternion.set(
                    quaternion.x,
                    -quaternion.y,
                    quaternion.z,
                    -quaternion.w
                );
                canvasMirror.scale.set(scale.x, -scale.y, scale.z);
                canvasMirror.matrixWorldNeedsUpdate = true;
                break;
                case "z":
                canvasMirror.position.set(position.x, position.y, -position.z);
                canvasMirror.quaternion.set(
                    quaternion.x,
                    quaternion.y,
                    -quaternion.z,
                    -quaternion.w
                );
                canvasMirror.scale.set(scale.x, scale.y, -scale.z);
                canvasMirror.matrixWorldNeedsUpdate = true;
                break;
                default:
                return;
            }
            }
            renderer.render(scene, camera);
        });
        controls.enabled = true;
        scene.add(controls);
        controls.attach(canvas);
        
        // Add click listener for selection
        // We'll attach to the renderer domElement or window.
        // vm (App.vue) handles generic input, but let's add specific logic here.
        renderer.domElement.addEventListener('pointerdown', this.onPointerDown);
        
        renderer.render(scene, camera);
        },
        onPointerDown(event) {
            if (!this.visible || !this.transformationEnabled) return;
            // Only select if tool is 'draw' or maybe always allowed if Gizmo is visible?
            // User request implies selection for "shapes".
            // If tool is 'draw', 'canvas' window is visible.
            // We should check if we clicked on a shape.
            
            const rect = renderer.domElement.getBoundingClientRect();
            const mouse = new THREE.Vector2();
            mouse.x = ((event.clientX - rect.left) / rect.width) * 2 - 1;
            mouse.y = -((event.clientY - rect.top) / rect.height) * 2 + 1;

            const raycaster = new THREE.Raycaster();
            raycaster.setFromCamera(mouse, camera);

            // Raycast against all shapes
            // Note: GLB models are Groups. Raycaster intersects meshes (recursive: true).
            // We need to find which 'shape' (root object in shapes array) was hit.
            const intersects = raycaster.intersectObjects(shapes, true);

            if (intersects.length > 0) {
                // Find the root shape that contains the intersected object
                const hitObject = intersects[0].object;
                let rootShape = null;
                
                // Climb up parents until we find an object in 'shapes'
                let curr = hitObject;
                while (curr) {
                    if (shapes.includes(curr)) {
                        rootShape = curr;
                        break;
                    }
                    curr = curr.parent;
                }

                if (rootShape) {
                    this.selectObject(rootShape);
                }
            }
        },
    resetTransformation() {
      canvas.position.set(
        this.startPosition.x,
        this.startPosition.y,
        this.startPosition.z
      );
      canvas.quaternion.set(
        this.startQuaternion.x,
        this.startQuaternion.y,
        this.startQuaternion.z,
        this.startQuaternion.w
      );
      canvas.scale.set(this.startScale.x, this.startScale.y, this.startScale.z);
      renderer.render(scene, camera);
      this.transformationResetDisabled = true;
    },
    toggleTransformMode() {
      if (this.mode === "combined") {
        this.mode = "scale";
        controls.mode = "scale";
        renderer.render(scene, camera);
      } else {
        this.mode = "combined";
        controls.mode = "combined";
        renderer.render(scene, camera);
      }
    },
    toggleControls() {
      if (controls.visible === true) {
        this.transformationEnabled = false;
        controls.enabled = false;
        controls.visible = false;
        renderer.render(scene, camera);
      } else {
        this.transformationEnabled = true;
        controls.enabled = true;
        controls.visible = true;
        renderer.render(scene, camera);
      }
    },
    toggleVisibility() {
      if (canvas.visible === true) {
        this.visible = false;
        this.$emit('visibility-change', false);

        if (this.transformationEnabled === true) {
          controls.visible = false;
        }

        if (controls.enabled === true) {
          controls.enabled = false;
        }

        canvas.visible = false;
        renderer.render(scene, camera);
      } else {
        this.visible = true;
        this.$emit('visibility-change', true);

        if (this.transformationEnabled === true) {
          controls.visible = true;
        }

        if (controls.enabled === false && this.transformationEnabled === true) {
          controls.enabled = true;
        }

        canvas.visible = true;

        renderer.render(scene, camera);
      }
    },
    toggleSnap() {
      if (this.snap === true) {
        this.snap = !this.snap;
        controls.translationSnap = null;
        controls.rotationSnap = null;
      } else {
        this.snap = !this.snap;
        controls.translationSnap = 1 / 3;
        controls.rotationSnap = Math.PI / 6;
      }
    },
    setUpMirror(val) {
      switch (val) {
        case "x":
          canvasMirror = canvas.clone();
          canvasMirror.applyMatrix4(canvas.matrixWorld.makeScale(-1, 1, 1));
          scene.add(canvasMirror);
          renderer.render(scene, camera);
          break;
        case "y":
          break;
        case "z":
          break;
        default:
          break;
      }
    },
    removeMirror() {
      scene.remove(canvasMirror);
    },
    setMaterial(type) {
         let newMat;
         if (type === 'standard') {
             newMat = new THREE.MeshToonMaterial({
                color: 0xe6edf5,
                transparent: true,
                opacity: 0.8,
                side: THREE.DoubleSide,
                polygonOffset: true,
                polygonOffsetFactor: 2.5,
                polygonOffsetUnits: -1,
                emissive: new THREE.Color("rgb(255,255,255)"),
                emissiveIntensity: 0.3,
            });
         } else {
             let texture;
             // Allow relative paths, http, or blobs. Basically anything that isn't 'standard'
             if (type && type !== 'standard') {
                 texture = new THREE.TextureLoader().load(type, () => {
                     renderer.render(scene, camera);
                 });
                 
                 newMat = new THREE.MeshMatcapMaterial({
                     color: 0xffffff, // Ensure base color is white
                     matcap: texture,
                     transparent: true,
                     opacity: 0.9,
                     side: THREE.DoubleSide
                 });
             } else {
                 return;
             }
         }
         
         this.material = newMat;
         
         // Update only the currently selected shape
         if (canvas) {
             if (canvas.isMesh) {
                 canvas.material = newMat; 
             } else if (canvas.isGroup) {
                 canvas.traverse(child => {
                     if (child.isMesh) child.material = newMat;
                 });
             }
         }
         renderer.render(scene, camera);
    },
  },
  watch: {
    opacity: function (val) {
      this.material.opacity = val;
      if (val == 0) {
        this.material.visible = false;
      } else {
        this.material.visible = true;
      }
      renderer.render(scene, camera);
    },

    selectedShape: function (val) {
      if (!val) return;
      if (val === 'custom') return; // Custom models are handled via loadCustomModel

      // Handle custom model selection by ID
      if (typeof val === 'object' && val.customId) {
          const found = shapes.find(s => s.userData && s.userData.customId === val.customId);
          if (found) {
              this.selectObject(found);
          }
          return;
      }

      // If the current canvas is a Group (from GLB), we must replace it with a Mesh
      // to allow applying primitive geometries.
      if (!canvas.isMesh) {
          // Keep reference to position/rotation/scale if we want to preserve them?
          // For now, let's keep them.
          const oldPos = canvas.position.clone();
          const oldQuat = canvas.quaternion.clone();
          const oldScale = canvas.scale.clone();
          
          // Remove from scene and shapes list
          scene.remove(canvas);
          const idx = shapes.indexOf(canvas);
          if (idx !== -1) shapes.splice(idx, 1);
          controls.detach();
          
          // Create new Mesh
          canvas = new THREE.Mesh(new THREE.PlaneGeometry(1, 1), this.material);
          canvas.position.copy(oldPos);
          canvas.quaternion.copy(oldQuat);
          canvas.scale.copy(oldScale);
          
          scene.add(canvas);
          shapes.push(canvas);
          controls.attach(canvas);
      }

      switch (val) {
        case "plane":
          if (canvas.geometry) canvas.geometry.dispose();
          canvas.geometry = new THREE.PlaneGeometry(5, 5);
          break;
        case "cube":
          if (canvas.geometry) canvas.geometry.dispose();
          canvas.geometry = new THREE.BoxGeometry(5, 5, 5);
          break;
        case "cylinder":
           if (canvas.geometry) canvas.geometry.dispose();
          canvas.geometry = new THREE.CylinderGeometry(2.5, 2.5, 5, 15);
          break;
        case "sphere":
           if (canvas.geometry) canvas.geometry.dispose();
          canvas.geometry = new THREE.SphereGeometry(2.5, 15, 15);
          break;
        case "head":
          if (canvas.geometry) canvas.geometry.dispose();
          if (headGeometry === undefined) {
            const loader = new GLTFLoader();
            loader.load("/asaro.glb", function (gltf) {
              headGeometry = gltf.scene.children[0].geometry;
              // Check if canvas is still valid/same
              if (canvas.geometry) canvas.geometry.dispose();
              canvas.geometry = headGeometry;
              canvas.geometry.needsUpdate = true;
              renderer.render(scene, camera);
            });
          } else {
            canvas.geometry = headGeometry;
            canvas.geometry.needsUpdate = true;
            renderer.render(scene, camera);
          }
          break;
        default:
          break;
      }
      
      if (canvas.geometry) canvas.geometry.needsUpdate = true;
      renderer.render(scene, camera);
    },

    mirror: function (val) {
      if (val === false) {
        this.removeMirror();
      } else {
        this.setUpMirror(val);
      }
    },
  },
};
</script>

<style scoped>
.canvas-controls {
  display: flex;
  align-items: center;
  gap: 4px;
  position: relative;
}

.separator {
  width: 1px;
  height: 32px;
  background-color: rgba(255, 255, 255, 0.1);
  margin: 0 4px;
}

.control-btn {
  position: relative;
  width: 40px;
  height: 40px;
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  padding: 0;
}

.control-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
}

.control-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.control-btn.active {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1);
}

.control-btn img {
  width: 24px;
  height: 24px;
  filter: brightness(0) invert(1);
}

/* Tooltip */
.tooltip {
  position: absolute;
  bottom: -32px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.9);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 11px;
  white-space: nowrap;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s;
  z-index: 1000;
}

.control-btn:hover .tooltip {
  opacity: 1;
}

/* Arrow for tooltip */
.tooltip::before {
  content: '';
  position: absolute;
  top: -4px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid rgba(0, 0, 0, 0.9);
}

/* Shape Dropdown */
.shape-dropdown {
  position: absolute;
  top: 48px;
  left: 0;
  display: flex;
  gap: 4px;
  background: rgba(30, 30, 30, 0.95);
  backdrop-filter: blur(16px);
  padding: 8px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  z-index: 100;
}

.shape-option {
  position: relative;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  padding: 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s;
}

.shape-option:hover {
  background: rgba(255, 255, 255, 0.15);
}

.shape-option img {
  width: 24px;
  height: 24px;
}
</style>