<template>
  <draggable-window 
    title="Colores" 
    :initialX="window.innerWidth - 550" 
    :initialY="160"
    :visible="visible"
    :reopenTrigger="reopenTrigger"
    :start-open="startOpen"
    :is-open="isOpen"
    @close="$emit('close')"
  >
    <div class="color-palette">
      <div v-for="(group, index) in colorGroups" :key="index" class="color-group">
        <div class="group-title">{{ group.name }}</div>
        <div class="colors-grid">
          <div 
            v-for="color in group.colors" 
            :key="color.name"
            class="color-item"
            :style="{ backgroundColor: color.hex }"
            @click="selectColor(color)"
            :title="color.name"
          ></div>
        </div>
      </div>
    </div>
  </draggable-window>
</template>

<script>
import DraggableWindow from "./DraggableWindow.vue";

export default {
  name: "ColorPalette",
  components: { DraggableWindow },
  props: {
    visible: Boolean,
    reopenTrigger: Number,
    startOpen: {
      type: Boolean,
      default: true,
    },
    isOpen: Boolean,
  },
  emits: ["set-color", "close"],
  data() {
    return {
      window: window,
      colorGroups: [
        {
          name: "GRIS CLARO  Volmenes iniciales",
          colors: [
            { name: "Gris Muy Claro", hex: "#F5F5F5" },
            { name: "Gris Claro", hex: "#E6E6E6" },
            { name: "Gris Clido Claro", hex: "#E8E2DC" },
            { name: "Gris Neutro Suave", hex: "#E1E1E1" },
          ]
        },
        {
          name: "GRIS MEDIO  Sombras principales",
          colors: [
            { name: "Gris Medio", hex: "#C8C8C8" },
            { name: "Gris Medio Clido", hex: "#CEC6BC" },
            { name: "Gris 3040%", hex: "#B4B4B4" },
          ]
        },
        {
          name: "GRIS OSCURO  Contraste",
          colors: [
            { name: "Gris Oscuro", hex: "#828282" },
            { name: "Gris Muy Oscuro", hex: "#5A5A5A" },
            { name: "Gris Oscuro Clido", hex: "#6E665F" },
            { name: "Gris 70%", hex: "#646464" },
          ]
        },
        {
          name: "NEGRO  Detalles finales",
          colors: [
            { name: "Negro trazado", hex: "#000000" },
            { name: "Negro suave", hex: "#141414" },
          ]
        },
        {
          name: "ACENTOS DE COLOR",
          colors: [
            { name: "Azul Tcnico Claro", hex: "#ADD8E6" },
            { name: "Azul Tcnico Medio", hex: "#6495ED" },
            { name: "Azul Conceptual", hex: "#4682B4" },
            { name: "Rojo Claro", hex: "#FF6347" },
            { name: "Rojo Industrial", hex: "#DC143C" },
            { name: "Naranja Industrial", hex: "#FF8C00" },
          ]
        },
        {
          name: "BLANCO  Highlights",
          colors: [
            { name: "Blanco puro", hex: "#FFFFFF" },
            { name: "Blanco suave", hex: "#FAFAFA" },
          ]
        }
      ]
    };
  },
  methods: {
    selectColor(color) {
      // Convert hex to rgb object for the app
      const rgb = this.hexToRgb(color.hex);
      this.$emit("set-color", rgb);
    },
    hexToRgb(hex) {
      var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
      return result ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16)
      } : null;
    }
  }
};
</script>

<style scoped>
.color-palette {
  display: flex;
  flex-direction: column;
  gap: 12px;
  color: white;
  min-width: 280px;
}

.group-title {
  font-size: 10px;
  text-transform: uppercase;
  opacity: 0.6;
  font-weight: bold;
  letter-spacing: 1px;
  margin-bottom: 6px;
}

.colors-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.color-item {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: transform 0.1s, border-color 0.1s;
}

.color-item:hover {
  transform: scale(1.1);
  border-color: white;
  z-index: 1;
}

.color-item:active {
  transform: scale(0.95);
}
</style>
