<template>
  <video-export-preview :show="previewing" @stop-previewing="setPreview" />
  <modal :modalProp="modalProp" @modal-set="setModal" @set-preset="applyPreset" />
  <viewport-cube
    v-show="uiVisible && windowStates.view"
    :quaternion="quaternion"
    :cameraResetDisabled="cameraResetDisabled"
    :reopenTrigger="windowTriggers.view"
    :is-open="windowStates.view"
    @close="onWindowClose('view')"
  />
  <tool-selector 
    v-show="uiVisible && windowStates.tools" 
    ref="toolSelector"
    @selected-tool="setSelectedTool" 
    @set-preset="applyPreset" 
    @toggle-properties="toggleProperties" 
    @toggle-scene="toggleScene" 
    :selectedTool="tool" 
    :reopenTrigger="windowTriggers.tools" 
    :is-open="windowStates.tools" 
    @close="onWindowClose('tools')"
    :initialX="windowPositions.tools ? windowPositions.tools.x : undefined"
    :initialY="windowPositions.tools ? windowPositions.tools.y : undefined" 
    @toggle-2d-shapes="toggleWindow('twoDShapes')"
  />
  <two-d-shapes 
    v-show="uiVisible && windowStates.twoDShapes" 
    :visible="true"
    :reopenTrigger="windowTriggers.twoDShapes" 
    :is-open="windowStates.twoDShapes" 
    @close="onWindowClose('twoDShapes')" 
    @add-shape="onAddCanvasShape"
  />
  <line-settings v-show="uiVisible && windowStates.properties" ref="lineSettings" @stroke="setStroke" @fill="setFill" :selectedTool="tool" :forceVisible="propertiesVisible" :reopenTrigger="windowTriggers.properties" :is-open="windowStates.properties" @close="onWindowClose('properties')" />

  <undo-redo v-show="uiVisible && windowStates.history" @selected-tool="setSelectedTool" ref="undoRedo" :reopenTrigger="windowTriggers.history" :is-open="windowStates.history" @close="onWindowClose('history')" />
  <Input
    @mouse-coordinates="setMouseCoordinates"
    :selectedTool="tool"
    :mirror="mirror"
    :stroke="stroke"
    :fill="fill"
    :radial="radial"
    :sectors="sectors"
    :radial-axis="radialAxis"
    :toolEnabled="toolEnabled"
    :localSymmetry="localSymmetry"
  />
  <Canvas
    ref="raycastCanvas"
    @selected-canvas-shape="setSelectedCanvasShape"
    @set-tool-enabled="setToolEnabled"
    :selectedShape="canvasShape"
    :selectedTool="tool"
    :mouse="mouse"
    :reopenTrigger="windowTriggers.canvas"
    :is-open="windowStates.canvas"
    @close="onWindowClose('canvas')"
    :initialX="windowPositions.canvas ? windowPositions.canvas.x : undefined"
    :initialY="windowPositions.canvas ? windowPositions.canvas.y : undefined"
  />
  <ShapeSelector
    v-show="uiVisible && windowStates.shapes"
    @selected-canvas-shape="setSelectedCanvasShape"
    @load-custom-model="loadCustomModelInCanvas"
    @set-material="setCanvasMaterial"
    :selectedShape="canvasShape"
    :selectedTool="tool"
    :reopenTrigger="windowTriggers.shapes"
    :is-open="windowStates.shapes"
    @close="onWindowClose('shapes')"
  />
  
  <Share v-show="uiVisible" @modal-set="setModal" @preview="setPreview" @toggle-window="toggleWindow" ref="share" :windowStates="windowStates" />
  <scene-settings v-show="uiVisible && windowStates.scene" :visible="true" :reopenTrigger="windowTriggers.scene" :is-open="windowStates.scene" @close="onWindowClose('scene')" />
  <color-palette v-show="uiVisible && windowStates.colors" :visible="true" :reopenTrigger="windowTriggers.colors" @set-color="applyColor" :is-open="windowStates.colors" @close="onWindowClose('colors')" />
  <symmetry-settings v-show="uiVisible && windowStates.symmetry" :visible="true" :reopenTrigger="windowTriggers.symmetry" :is-open="windowStates.symmetry" @close="onWindowClose('symmetry')" :mirror="mirror" @set-mirror="mirror = $event" :radial="radial" @set-radial="radial = $event" :sectors="sectors" @set-sectors="sectors = $event" :radial-axis="radialAxis" @set-radial-axis="radialAxis = $event" :local-symmetry="localSymmetry" @set-local-symmetry="localSymmetry = $event" />
    <layers v-show="uiVisible && windowStates.layers" :visible="true" :reopenTrigger="windowTriggers.layers" :is-open="windowStates.layers" @close="onWindowClose('layers')" @layer-add="onLayerAdd" @layer-delete="onLayerDelete" @layer-select="onLayerSelect" @layer-change="onLayerChange" @layer-duplicate="onLayerDuplicate" />
    <object-manager v-show="uiVisible && windowStates.objectManager" :visible="true" :reopenTrigger="windowTriggers.objectManager" :is-open="windowStates.objectManager" @close="onWindowClose('objectManager')" />
    
    <transform-toolbar
      v-show="uiVisible"
      :top="transformToolbar.top"
      :left="transformToolbar.left"
      :location="transformToolbar.location"
      :display="transformToolbar.display"
      :selectedTool="tool"
    />
  
  <!-- <div>
    <a id="ar" rel="ar"><img src="@/assets/icons/AR.png" /></a>
  </div> -->
</template>

<script>
import * as THREE from "three";
//import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import CameraControls from "camera-controls";
CameraControls.install({ THREE: THREE });
import InfiniteGridHelper from "./components/InfiniteGridHelper.js";

import Input from "./components/Input.vue";
import ToolSelector from "./components/ToolSelector.vue";
import ViewportCube from "./components/ViewportCube.vue";
import UndoRedo, { undoManager } from "./components/UndoRedo.vue";
import { select } from "./components/select.js";
import { draw } from "./components/draw.js";
import { erase } from "./components/erase.js";
import LineSettings from "./components/LineSettings.vue";

import Share from "./components/Share.vue";
import Canvas from "./components/Canvas.vue";
import { controls, canvas } from "./components/Canvas.vue";
import ShapeSelector from "./components/ShapeSelector.vue";
import Modal from "./components/Modal.vue";
import VideoExportPreview from "./components/VideoExportPreview.vue";
import SceneSettings from "./components/SceneSettings.vue";
import ColorPalette from "./components/ColorPalette.vue";
import SymmetrySettings from "./components/SymmetrySettings.vue";

import Layers from "./components/Layers.vue";
import ObjectManager from "./components/ObjectManager.vue";
import TransformToolbar from "./components/TransformToolbar.vue";
import TwoDShapes from "./components/TwoDShapes.vue";

export let render = new THREE.WebGLRenderer({
  antialias: true,
  alpha: true,
  preserveDrawingBuffer: true,
});
// Rename export to avoid conflict (or just keep it and patch it later)
export let renderer = render; 

export let camera = new THREE.PerspectiveCamera(
  100,
  window.innerWidth / window.innerHeight,
  1,
  1000
);

export let scene, drawingScene, cameraControls, vm, drawingprop, composer;

var main, clock;

export default {
  name: "App",
  // ... (components list unchanged)
  components: {
    Input,
    LineSettings,
    ToolSelector,
    ViewportCube,
    UndoRedo,
    Share,
    Canvas,
    ShapeSelector,
    Modal,
    VideoExportPreview,
    SceneSettings,
    ColorPalette,
    SymmetrySettings,

    Layers,
    ObjectManager,
    TransformToolbar,
    TwoDShapes,
  },
  data() {
    return {
      // ... (data unchanged)
      tool: "draw",
      toolEnabled: true,
      canvasShape: "plane",
      multitouch: "canvas",
      toolHistory: ["draw"],
      stroke: {}, 
      fill: {}, 
      mirror: "", 
      radial: false,
      sectors: 6,
      radialAxis: 'y',
      localSymmetry: false,
      quaternion: undefined,
      cameraResetDisabled: false,
      selected: undefined,
      transformToolbar: { top: 0, left: 0, location: "above", display: false },
      canvasTransformEnabled: true,
      modalProp: { show: false, mode: "about" },
      previewing: false,
      mouse: {},
      propertiesVisible: false,
      layerGroups: {},
      activeLayerId: 1,
      windowTriggers: {
        tools: 0,
        properties: 0,
        canvas: 0,
        shapes: 0,
        history: 0,
        view: 0,
        actions: 0,
        scene: 0,
        colors: 0,
        symmetry: 0,
        layers: 0,
        objectManager: 0,
        twoDShapes: 0,
      },
      windowPositions: {},
      windowStates: {
        tools: true,
        properties: false,
        canvas: true,
        shapes: false,
        history: false,
        view: true,
        scene: false,
        colors: false,
        symmetry: false,
        layers: false,
        objectManager: false,
        twoDShapes: false,
      },
      uiVisible: true,
    };
  },
  emits: ["modal-set", "selected-canvas-shape"],
  methods: {
    toggleProperties: function() {
      this.toggleWindow('properties');
    },
    toggleScene: function() {
      this.toggleWindow('scene');
    },
    toggleWindow: function(windowName) {
      if (this.windowStates[windowName] !== undefined) {
          this.windowStates[windowName] = !this.windowStates[windowName];
          if (this.windowStates[windowName] && this.windowTriggers[windowName] !== undefined) {
            this.windowTriggers[windowName]++;
          }
      } else {
          if (this.windowTriggers[windowName] !== undefined) {
            this.windowTriggers[windowName]++;
          }
      }
    },
    onWindowClose: function(windowName) {
       if (this.windowStates[windowName] !== undefined) {
           this.windowStates[windowName] = false;
       }
    },
    init: function () {
      CameraControls.install({ THREE: THREE });
      main = document.getElementById("threed");

      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.setClearColor(0x000000, 0); 
      renderer.setSize(window.innerWidth, window.innerHeight);
      main.appendChild(renderer.domElement);
      renderer.domElement.id = "threeJsCanvas";

      scene = new THREE.Scene();
      var bgCol = 0xdddddd;
      scene.background = new THREE.Color(bgCol);
      scene.fog = new THREE.Fog(bgCol, 10, 50);

      drawingScene = new THREE.Scene();

      const grid = new InfiniteGridHelper(1, 10, new THREE.Color(0x0000ff), 40);
      scene.add(grid);

      camera.layers.enable(0); 
      camera.layers.enable(1);
      camera.zoom = 3;
      camera.position.set(5, 5, 10);

      clock = new THREE.Clock();

      // Post-Processing Removed to restore clean look
      /* 
      const renderScene = new RenderPass(scene, camera);
      const bloomPass = new UnrealBloomPass(...)
      */

      cameraControls = new CameraControls(camera, renderer.domElement);
      cameraControls.dampingFactor = 20;
      cameraControls.draggingDampingFactor = 200;
      cameraControls.mouseButtons.left = CameraControls.ACTION.ROTATE;
      cameraControls.mouseButtons.wheel = CameraControls.ACTION.DOLLY;
      document.addEventListener("keydown", (event) => {
        if (event.code === "Space") {
          cameraControls.mouseButtons.left = CameraControls.ACTION.TRUCK;
          this.toolEnabled = false;
        }
      });
      document.addEventListener("keyup", (event) => {
        if (event.code === "Space") {
          cameraControls.mouseButtons.left = CameraControls.ACTION.ROTATE;
          this.toolEnabled = true;
        }
      });
      document.addEventListener("keydown", (event) => {
        if (event.code === "AltLeft") {
          cameraControls.mouseButtons.left = CameraControls.ACTION.ROTATE;
          this.toolEnabled = false;
        }
      });
      document.addEventListener("keyup", (event) => {
        if (event.code === "AltLeft") {
          cameraControls.mouseButtons.left = CameraControls.ACTION.ROTATE;
          this.toolEnabled = true;
        }
      });
      cameraControls.mouseButtons.middle = CameraControls.ACTION.TRUCK;
      cameraControls.mouseButtons.right = CameraControls.ACTION.NONE;
      cameraControls.touches.one = CameraControls.ACTION.TOUCH_ROTATE;
      cameraControls.touches.two = CameraControls.ACTION.TOUCH_DOLLY_TRUCK;
      cameraControls.touches.three = CameraControls.ACTION.TOUCH_ZOOM_TRUCK;
      cameraControls.maxZoom = 4000;
      cameraControls.minZoom = 0.5;
      cameraControls.enabled = true;

      this.quaternion = [
        camera.quaternion.x,
        camera.quaternion.y,
        camera.quaternion.z,
        camera.quaternion.w,
      ];

      cameraControls.addEventListener("update", () => {
        this.quaternion = [
          camera.quaternion.x,
          camera.quaternion.y,
          camera.quaternion.z,
          camera.quaternion.w,
        ];

        let target = new THREE.Vector3();
        target = cameraControls.getTarget(target);
        targetSphere.position.set(target.x, target.y, target.z);

        if (select.s && select.s.controls != undefined) {
          if (this.tool == "select") {
            select.s.helper.update();
          }
        }
      });

      cameraControls.addEventListener("sleep", () => {
        if (select.s && select.s.controls != undefined) {
          if (this.tool == "select") {
            select.s.helper.update();
            let position = select.s.calculateTransfromToolbarPosition();
            this.transformToolbar.left = position.x;
            this.transformToolbar.top = position.y;
            this.transformToolbar.location = position.location;
            this.transformToolbar.display = true;
          }
        }

        let target = new THREE.Vector3();
        target = cameraControls.getTarget(target);
        var x = target.x;
        var y = target.y;
        var z = target.z;

        if (
          camera.position.z != 10 ||
          x != 0 ||
          y != 0 ||
          z != 0 ||
          camera.zoom != 3
        ) {
          this.cameraResetDisabled = false;
        } else {
          this.cameraResetDisabled = true;
        }
      });

      var geometry = new THREE.SphereBufferGeometry(0.025, 32, 32);
      var material = new THREE.MeshBasicMaterial({ color: 0xffc75f });
      var targetSphere = new THREE.Mesh(geometry, material);
      scene.add(targetSphere);

      const light = new THREE.HemisphereLight(0xffffff, 0x222222, 0.8);
      light.position.set(0, 50, 150);
      scene.add(light);

      this.$.refs.raycastCanvas.setUp();
      this.$.refs.raycastCanvas.resetTransformation();

      window.addEventListener("resize", this.onWindowResize);
      window.addEventListener("orientationchange", this.onWindowResize);
      this.onWindowResize();

      renderer.render(scene, camera);
    },
    onWindowResize: function () {
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.left = (900 * camera.aspect) / -2;
      camera.right = (900 * camera.aspect) / 2;
      camera.top = 900 / 2;
      camera.bottom = -900 / 2;

      camera.updateProjectionMatrix();

      renderer.setSize(window.innerWidth, window.innerHeight);

      renderer.setViewport(0, 0, window.innerWidth, window.innerHeight);
      renderer.render(scene, camera);
    },
    animate: function () {
      const delta = clock.getDelta();
      let hasCameraControlsUpdated = cameraControls.update(delta);
      let linesAnimated = false;
      if (draw && draw.animateLines) {
           linesAnimated = draw.animateLines(delta);
      }

      requestAnimationFrame(this.animate);

      if (hasCameraControlsUpdated || linesAnimated) {
        renderer.render(scene, camera);
      }
    },
    setSelectedTool: function (val) {
      this.tool = val;

      if (this.tool === "erase") {
        canvas.visible = false;
        controls.visible = false;
        renderer.render(scene, camera);
      } else if (this.tool === "draw") {
        if (this.$.refs.raycastCanvas.visible === true) {
          canvas.visible = true;
        } else {
          canvas.visible = false;
        }
        if (
          this.$.refs.raycastCanvas.transformationEnabled === true &&
          this.$.refs.raycastCanvas.visible === true
        ) {
          controls.visible = true;
        } else {
          controls.visible = false;
        }
        renderer.render(scene, camera);
      }

      this.toolHistory.push(val);
      if (this.toolHistory.length > 2) {
        this.toolHistory.shift();
      }

      let current = val;
      let previous = this.toolHistory[0];

      undoManager.add({
        undo: function () {
          vm.setSelectedTool(previous);
        },
        redo: function () {
          vm.setSelectedTool(current);
        },
      });

      this.$.refs.undoRedo.updateUi();
    },
    setToolEnabled: function (val) {
      this.toolEnabled = val;
    },
    setMouseCoordinates: function (val) {
      this.mouse = val;
    },
    setSelectedCanvasShape: function (val) {
      this.canvasShape = val;
    },
    loadCustomModelInCanvas: function (url) {
      if (this.$refs.raycastCanvas) {
        this.$refs.raycastCanvas.loadCustomModel(url);
      }
    },
    setCanvasMaterial: function (val) {
      if (this.$refs.raycastCanvas) {
        this.$refs.raycastCanvas.setMaterial(val);
      }
    },
    onAddCanvasShape: function(mesh) {
        // Switch to Canvas mode
        this.setSelectedTool('draw');
        // Forward to Canvas component
        if (this.$refs.raycastCanvas) {
            this.$refs.raycastCanvas.addShape(mesh);
        }
    },
    setSelectedTool_internal: function (val) {
      this.tool = val;
    },
    setSelectedMultitouch: function (val) {
      this.multitouch = val;

      if (val === "camera") {
        cameraControls.enabled = true;
      } else {
        cameraControls.enabled = false;
      }
    },
    setSelectedObject: function (val) {
      this.selected = val;
    },
    setTransformToolbarPosition: function (val) {
      this.transformToolbar.top = val.top;
      this.transformToolbar.left = val.left;
      this.transformToolbar.location = val.location;
    },
    setTransformToolbarDisplay: function (val) {
      this.transformToolbar.display = val;
    },
    setStroke: function (val) {
      this.stroke = val;
      if (select && select.s && select.s.controls && select.s.controls.object && select.s.controls.object.type === 'Mesh') {
           draw.updateStroke(select.s.controls.object, val);
           renderer.render(scene, camera);
      }
    },
    setFill: function (val) {
      this.fill = val;
      if (select && select.s && select.s.controls && select.s.controls.object && select.s.controls.object.type === 'Mesh') {
           draw.updateFill(select.s.controls.object, val);
           renderer.render(scene, camera);
      }
    },
    setModal: function (val) {
      this.modalProp = val;
    },
    setPreview: function (val) {
      this.previewing = val;
    },
    applyPreset: function (settings) {
      this.$refs.lineSettings.updateSettings(settings);
    },
    applyColor: function(color) {
      if (this.tool !== 'draw') {
        this.tool = 'draw';
      }
      this.$refs.lineSettings.updateSettings({
        strokeColor: color,
        stroke: true 
      });
    },
    handleKeyDown(event) {
        if (event.key === "AltGraph") {
            this.uiVisible = !this.uiVisible;
        }
        if (event.code === "Space") {
            if (this.$refs.raycastCanvas) {
                this.$refs.raycastCanvas.toggleVisibility();
            }
        }
        if (event.code === "Delete" || event.code === "Backspace") {
            if (select && select.s && select.s.controls && select.s.controls.object) {
                 const obj = select.s.controls.object;
                 let objectsToDelete = [];

                 if (obj.type === "Group") {
                     // Multiple selection
                     obj.children.forEach(child => {
                         // The group children are clones/helpers, the real UUID is in userData
                         const uuid = child.userData.uuid;
                         if (uuid) {
                             // Find the real object in the LayerGroups or Scene
                             // We can search the whole scene or use getObjectByProperty on scene
                             // Note: erase.deleteObject expects the object itself
                             const realArg = scene.getObjectByProperty("uuid", uuid);
                             if (realArg) objectsToDelete.push(realArg);
                         }
                     });
                 } else {
                     // Single selection (Mesh)
                     objectsToDelete.push(obj);
                 }

                 // Deselect first to remove gizmos
                 if (objectsToDelete.length > 0) {
                     select.s.deselect();
                     
                     // Now delete
                     objectsToDelete.forEach(o => {
                         erase.deleteObject(o);
                     });
                     
                     renderer.render(scene, camera);
                 }
            }
        }
    },
    onLayerAdd(layer) {
      const layerGroup = new THREE.Group();
      layerGroup.name = `Layer_${layer.id}`;
      layerGroup.userData.layerId = layer.id;
      scene.add(layerGroup);
      this.layerGroups[layer.id] = layerGroup;
    },
    onLayerDelete(layer) {
      const layerGroup = this.layerGroups[layer.id];
      if (layerGroup) {
        while(layerGroup.children.length > 0) {
          layerGroup.remove(layerGroup.children[0]);
        }
        scene.remove(layerGroup);
        delete this.layerGroups[layer.id];
        renderer.render(scene, camera);
      }
    },
    onLayerSelect(layer) {
      this.activeLayerId = layer.id;
    },
    onLayerChange(layer) {
      const layerGroup = this.layerGroups[layer.id];
      if (layerGroup) {
        layerGroup.visible = layer.visible;
        layerGroup.traverse((child) => {
          if (child.isMesh && child.userData && child.userData.stroke) {
               // Update stroke opacity
               let opacity = layer.opacity / 100;
               // If item has its own alpha, multiply? For now, override or multiply with base stroke opacity
               let strokeOpacity = child.userData.stroke.opacity !== undefined ? child.userData.stroke.opacity : 1;
               child.material.opacity = strokeOpacity * opacity;
               child.material.transparent = true;
               if(child.material.uniforms && child.material.uniforms.opacity) {
                   child.material.uniforms.opacity.value = child.material.opacity;
               }
          } else if (child.material) {
            // General objects
            let opacity = layer.opacity / 100;
            if (Array.isArray(child.material)) {
              child.material.forEach(mat => {
                mat.opacity = opacity;
                mat.transparent = opacity < 1;
              });
            } else {
              child.material.opacity = opacity;
              child.material.transparent = opacity < 1;
            }
          }
        });
        renderer.render(scene, camera);
      }
    },
    onLayerDuplicate(payload) {
        // payload: { newLayer, originalLayerId }
        const originalGroup = this.layerGroups[payload.originalLayerId];
        if (!originalGroup) return;

        const newGroup = new THREE.Group();
        newGroup.name = `Layer_${payload.newLayer.id}`;
        newGroup.userData.layerId = payload.newLayer.id;

        // Deep clone children
        originalGroup.children.forEach(child => {
            const clone = child.clone(true); // Recursive clone
            
            // We must update UUIDs for the system to treat them as new unique objects
            // Also need to update userData logic if it references UUIDs
            clone.uuid = THREE.MathUtils.generateUUID();
            
            // If it's a stroke, we might want to ensure the material is unique if we plan to change it independently later?
            // Usually cloning mesh shares material. 
            // For independent layer opacity, sharing material is tricky if we modify material opacity directly.
            // But we do modify directly in onLayerChange. So we should clone materials too.
            if (clone.material) {
                clone.material = clone.material.clone();
            }
            
            newGroup.add(clone);
        });

        scene.add(newGroup);
        this.layerGroups[payload.newLayer.id] = newGroup;
        renderer.render(scene, camera);
    },
    getActiveLayerGroup() {
      return this.layerGroups[this.activeLayerId];
    },
  },
  watch: {
    'windowStates.properties': function(val) {
        this.propertiesVisible = val;
    }
  },
  mounted() {
    this.init();
    window.addEventListener("keydown", this.handleKeyDown);
    this.animate();

    this.onLayerAdd({ id: 1, name: "Layer 1", visible: true, opacity: 100, locked: false });

    vm = this;
    this.cameraResetDisabled = false; 
  },
  beforeUnmount() {
      window.removeEventListener("keydown", this.handleKeyDown);
  }
};
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;800&display=swap');

:root {
  --bg-color: rgb(60, 57, 60);
  --accent-color: rgb(255, 176, 0);
  --accent-color-selected: rgb(228, 156, 0);
  --ui-bg-color: rgb(30, 30, 30);
  --line-color-lightest: rgb(255, 242, 213);
  --line-color-light: rgb(255, 231, 178);
  --line-color-medium: rgb(255, 220, 142);
  --line-color-dark: rgb(255, 209, 107);
}

body,
html,
#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  border: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  font-weight: 900;
  color: #1c1c1e;
}

button {
  font-weight: 900;
  color: #1c1c1e;
}

#app {
  font-family: 'Inter', Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
