
<template>
  <draggable-window title="History" :initialX="12" :initialY="window.innerHeight - 100" :reopenTrigger="reopenTrigger" :start-open="startOpen" :is-open="isOpen" @close="$emit('close')">
    <div class="history-controls">
      <button @click="undo" :disabled="undoDisabled">
        <img src="@/assets/icons/undo.svg" alt="undo" />
      </button>
      <button @click="redo" :disabled="redoDisabled">
        <img src="@/assets/icons/redo.svg" alt="redo" />
      </button>
    </div>
  </draggable-window>
</template>

<script>
import UndoManager from "undo-manager";
import DraggableWindow from "./DraggableWindow.vue";

export let undoManager = new UndoManager();
export let undoRedoComponent;

export default {
  name: "UndoRedo",
  components: { DraggableWindow },
  props: {
    reopenTrigger: Number,
    startOpen: {
      type: Boolean,
      default: true,
    },
    isOpen: Boolean,
  },
  emits: ["selected-tool", "close"],
  data() {
    return {
      window: window,
      undoDisabled: true,
      redoDisabled: true,
    };
  },
  methods: {
    undo: function () {
      undoManager.undo();
      this.updateUi();
    },
    redo: function () {
      undoManager.redo();
      this.updateUi();
    },
    setTool: function (val) {
      this.$emit("selected-tool", val);
    },
    updateUi: function () {
      this.undoDisabled = !undoManager.hasUndo();
      this.redoDisabled = !undoManager.hasRedo();
    },
  },
  watch: {},
  mounted() {
    undoRedoComponent = this;
  },
};
</script>

<style scoped>
.history-controls {
  display: flex;
}

button {
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px; /* Reset radius for individual buttons */
  height: 40px;
  width: 40px; /* Make them square */
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  padding: 0;
  cursor: pointer;
  margin-right: 4px;
}

button:last-child {
  margin-right: 0;
}

button:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
}

button:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

button > img {
  width: 24px;
  height: 24px;
  filter: brightness(0) invert(1);
}
</style>