<template>
  <draggable-window 
    title="Layers" 
    :initialX="12" 
    :initialY="520"
    :visible="visible"
    :reopenTrigger="reopenTrigger"
    :is-open="isOpen"
    @close="$emit('close')"
  >
    <div class="layers-container">
      <div class="layers-header">
        <button class="layer-btn" @click="addLayer" title="Add Layer">
          <span>+</span>
        </button>
        <button class="layer-btn" @click="deleteLayer" title="Delete Layer" :disabled="layers.length <= 1">
          <span></span>
        </button>
        <button class="layer-btn" @click="duplicateLayer" title="Duplicate Layer">
          <span></span>
        </button>
      </div>
      
      <div class="layers-list">
        <div 
          v-for="layer in layers" 
          :key="layer.id"
          class="layer-item"
          :class="{ active: layer.id === activeLayerId }"
          @click="selectLayer(layer.id)"
        >
          <div class="layer-visibility">
            <button 
              class="visibility-btn" 
              @click.stop="toggleVisibility(layer.id)"
              :class="{ hidden: !layer.visible }"
            >
              {{ layer.visible ? '' : '' }}
            </button>
          </div>
          
          <div class="layer-info">
            <input 
              v-if="editingLayerId === layer.id"
              v-model="layer.name"
              @blur="stopEditing"
              @keyup.enter="stopEditing"
              @click.stop
              class="layer-name-input"
              ref="nameInput"
            />
            <span 
              v-else
              class="layer-name"
              @dblclick.stop="startEditing(layer.id)"
            >
              {{ layer.name }}
            </span>
          </div>
          
          <div class="layer-opacity">
            <input 
              type="range" 
              min="0" 
              max="100" 
              v-model="layer.opacity"
              @input="updateLayerOpacity(layer.id, $event.target.value)"
              @click.stop
              class="opacity-slider"
            />
            <span class="opacity-value">{{ layer.opacity }}%</span>
          </div>
        </div>
      </div>
      

    </div>
  </draggable-window>
</template>

<script>
import DraggableWindow from "./DraggableWindow.vue";

export default {
  name: "Layers",
  components: { DraggableWindow },
  props: {
    visible: Boolean,
    reopenTrigger: Number,
    isOpen: Boolean,
  },
  emits: ["close", "layer-change", "layer-add", "layer-delete", "layer-select"],
  data() {
    return {
      layers: [
        { id: 1, name: "Layer 1", visible: true, opacity: 100, locked: false }
      ],
      activeLayerId: 1,
      nextLayerId: 2,
      editingLayerId: null,
      lockLayers: false,
      mergeOnExport: false,
    };
  },
  methods: {
    addLayer() {
      const newLayer = {
        id: this.nextLayerId++,
        name: `Layer ${this.nextLayerId - 1}`,
        visible: true,
        opacity: 100,
        locked: false
      };
      this.layers.unshift(newLayer);
      this.selectLayer(newLayer.id);
      this.$emit("layer-add", newLayer);
    },
    
    deleteLayer() {
      if (this.layers.length <= 1) return;
      
      const index = this.layers.findIndex(l => l.id === this.activeLayerId);
      if (index !== -1) {
        const deletedLayer = this.layers[index];
        this.layers.splice(index, 1);
        
        // Select the layer below or above
        if (this.layers.length > 0) {
          const newIndex = Math.min(index, this.layers.length - 1);
          this.selectLayer(this.layers[newIndex].id);
        }
        
        this.$emit("layer-delete", deletedLayer);
      }
    },
    
    duplicateLayer() {
      const layer = this.layers.find(l => l.id === this.activeLayerId);
      if (layer) {
        const newLayer = {
          id: this.nextLayerId++,
          name: `${layer.name} Copy`,
          visible: layer.visible,
          opacity: layer.opacity,
          locked: layer.locked
        };
        const index = this.layers.findIndex(l => l.id === this.activeLayerId);
        this.layers.splice(index, 0, newLayer);
        this.selectLayer(newLayer.id);
        this.$emit("layer-duplicate", { newLayer, originalLayerId: layer.id });
        this.$emit("layer-add", newLayer);
      }
    },
    
    selectLayer(layerId) {
      this.activeLayerId = layerId;
      const layer = this.layers.find(l => l.id === layerId);
      this.$emit("layer-select", layer);
    },
    
    toggleVisibility(layerId) {
      const index = this.layers.findIndex(l => l.id === layerId);
      if (index !== -1) {
        const layer = this.layers[index];
        // Toggle between opacity 0 (hidden) and 100 (visible)
        const newOpacity = layer.opacity === 0 ? 100 : 0;
        this.layers[index].opacity = newOpacity;
        // Also update visible for UI feedback
        this.layers[index].visible = newOpacity > 0;
        
        console.log(`Layer ${layerId} opacity toggled to:`, newOpacity);
        console.log('Emitting layer-change event with:', this.layers[index]);
        this.$emit("layer-change", this.layers[index]);
        console.log('layer-change event emitted');
      }
    },
    
    updateLayerOpacity(layerId, opacity) {
      const layer = this.layers.find(l => l.id === layerId);
      if (layer) {
        layer.opacity = parseInt(opacity);
        this.$emit("layer-change", layer);
      }
    },
    
    startEditing(layerId) {
      this.editingLayerId = layerId;
      this.$nextTick(() => {
        if (this.$refs.nameInput && this.$refs.nameInput[0]) {
          this.$refs.nameInput[0].focus();
          this.$refs.nameInput[0].select();
        }
      });
    },
    
    stopEditing() {
      this.editingLayerId = null;
    },
    
    toggleLockLayers() {
      this.layers.forEach(layer => {
        layer.locked = this.lockLayers;
      });
      this.$emit("layer-change", { lockAll: this.lockLayers });
    }
  },
};
</script>

<style scoped>

.layers-container {
  background: rgba(30, 30, 30, 0.95);
  border-radius: 8px;
  padding: 6px;
  min-width: 220px;
  max-width: 260px;
}

.layers-header {
  display: flex;
  gap: 6px;
  margin-bottom: 8px;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.layer-btn {
  flex: 1;
  height: 28px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  color: white;
  font-weight: 600;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.layer-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15);
}

.layer-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.layers-list {
  max-height: 300px;
  overflow-y: auto;
  margin-bottom: 8px;
}

.layer-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  padding: 6px;
  margin-bottom: 4px;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
}

.layer-item:hover {
  background: rgba(255, 255, 255, 0.1);
}

.layer-item.active {
  background: var(--accent-color, #ffb000);
  border-color: var(--accent-color, #ffb000);
}

.layer-item.active .layer-name,
.layer-item.active .opacity-value {
  color: black;
  font-weight: 700;
}

.layer-visibility {
  display: flex;
  align-items: center;
  z-index: 10;
  position: relative;
}

.visibility-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 16px;
  padding: 4px;
  transition: all 0.2s;
  pointer-events: auto;
  z-index: 11;
  position: relative;
  min-width: 20px;
  min-height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.visibility-btn:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

.visibility-btn.hidden {
  opacity: 0.4;
}

.layer-info {
  flex: 1;
  display: flex;
  align-items: center;
}

.layer-name {
  color: white;
  font-size: 14px;
  font-weight: 500;
  user-select: none;
}

.layer-name-input {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  color: white;
  padding: 4px 8px;
  font-size: 14px;
  width: 100%;
}

.layer-opacity {
  display: flex;
  align-items: center;
  gap: 8px;
}

.opacity-slider {
  width: 60px;
  flex: none;
  height: 4px;
  border-radius: 2px;
  background: rgba(255, 255, 255, 0.2);
  outline: none;
  -webkit-appearance: none;
}

.opacity-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
}

.opacity-slider::-moz-range-thumb {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
  border: none;
}

.opacity-value {
  color: white;
  font-size: 11px;
  min-width: 35px;
  text-align: right;
}

.layer-item.active .opacity-value {
  color: black;
}



/* Scrollbar styling */
.layers-list::-webkit-scrollbar {
  width: 6px;
}

.layers-list::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 3px;
}

.layers-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 3px;
}

.layers-list::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.3);
}
</style>
