<template>
  <div class="brush-library">
    <h2>Brush Library</h2>
    <div class="presets">
      <div class="preset" @click="selectPreset('pencil')">
        <div class="icon"></div>
        <div class="info">
          <h3>Pencil</h3>
          <p>Fine lines for sketching.</p>
        </div>
      </div>
      <div class="preset" @click="selectPreset('pen')">
        <div class="icon"></div>
        <div class="info">
          <h3>Pen</h3>
          <p>Clean lines for inking.</p>
        </div>
      </div>
      <div class="preset" @click="selectPreset('marker')">
        <div class="icon"></div>
        <div class="info">
          <h3>Marker</h3>
          <p>Thick lines for coloring.</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BrushLibrary",
  emits: ["set-preset"],
  methods: {
    selectPreset(type) {
      let settings = {};
      switch (type) {
        case "pencil":
          settings = {
            strokeWidth: 2,
            strokeColor: { r: 50, g: 50, b: 50 },
            stroke: true,
            fill: false
          };
          break;
        case "pen":
          settings = {
            strokeWidth: 5,
            strokeColor: { r: 0, g: 0, b: 0 },
            stroke: true,
            fill: false
          };
          break;
        case "marker":
          settings = {
            strokeWidth: 20,
            strokeColor: { r: 255, g: 0, b: 0 }, // Red marker by default? Or maybe blue?
            stroke: true,
            fill: false
          };
          break;
      }
      this.$emit("set-preset", settings);
    },
  },
};
</script>

<style scoped>
.brush-library {
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
}

.presets {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.preset {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 12px;
  background-color: #f5f5f5;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.preset:hover {
  background-color: #ffe8b3;
}

.icon {
  font-size: 2em;
}

.info h3 {
  margin: 0 0 4px 0;
  font-size: 1.1em;
}

.info p {
  margin: 0;
  color: #666;
  font-size: 0.9em;
  font-weight: normal;
}
</style>
