import * as THREE from 'three'

// Author: Fyrestar https://mevedia.com (https://github.com/Fyrestar/THREE.InfiniteGridHelper)
// Author: Fyrestar https://mevedia.com (https://github.com/Fyrestar/THREE.InfiniteGridHelper)
export default function InfiniteGridHelper(size1, size2, color, distance, axes = 'xzy', color2, distanceNear = 0) {

    color = color || new THREE.Color('white');
    color2 = color2 || new THREE.Color('white'); // Default second color
    size1 = size1 || 10;
    size2 = size2 || 100;

    distance = distance || 8000;
    distanceNear = distanceNear || 0;



    const planeAxes = axes.substr(0, 2);

    const geometry = new THREE.PlaneBufferGeometry(2, 2, 1, 1);

    const material = new THREE.ShaderMaterial({

        side: THREE.DoubleSide,

        uniforms: {
            uSize1: {
                value: size1
            },
            uSize2: {
                value: size2
            },
            uColor: {
                value: color
            },
            uColor2: {
                value: color2
            },
            uDistance: {
                value: distance
            },
            uDistanceNear: {
                value: distanceNear
            }
        },
        transparent: true,
        vertexShader: `
           
           varying vec3 worldPosition;
		   
           uniform float uDistance;
           
           void main() {
           
                vec3 pos = position.${axes} * uDistance;
                pos.${planeAxes} += cameraPosition.${planeAxes};
                
                worldPosition = pos;
                
                gl_Position = projectionMatrix * modelViewMatrix * vec4(pos, 1.0);
           
           }
           `,


        fragmentShader: `
           
           varying vec3 worldPosition;
           
           uniform float uSize1;
           uniform float uSize2;
           uniform vec3 uColor;
           uniform vec3 uColor2;
           uniform float uDistance;
           uniform float uDistanceNear;
            
            
            
            float getGrid(float size) {
            
                vec2 r = worldPosition.${planeAxes} / size;
                
                
                vec2 grid = abs(fract(r - 0.5) - 0.5) / fwidth(r);
                float line = min(grid.x, grid.y);
                
            
                return 1.0 - min(line, 1.0);
            }
            
           void main() {
           
                
                  float dist = distance(cameraPosition.${planeAxes}, worldPosition.${planeAxes});
                  float d = clamp((uDistance - dist) / (uDistance - uDistanceNear), 0.0, 1.0);
                
                  float g1 = getGrid(uSize1);
                  float g2 = getGrid(uSize2);
                  
                  // Mix colors: if g2 (large grid) is present, use uColor2, else use uColor
                  vec3 color = mix(uColor, uColor2, g2); 
                  
                  gl_FragColor = vec4(color.rgb, mix(g2, g1, g1) * pow(d, 3.0));
                  gl_FragColor.a = mix(0.5 * gl_FragColor.a, gl_FragColor.a, g2);
                
                  if ( gl_FragColor.a <= 0.0 ) discard;
                
           
           }
           
           `,

        extensions: {
            derivatives: true
        }

    });


    // THREE.Mesh.call( this, geometry, material );

    // this.frustumCulled = false;

    let grid = new THREE.Mesh(geometry, material);

    grid.frustumCulled = false;

    return grid

}

InfiniteGridHelper.prototype = {
    ...THREE.Mesh.prototype,
    ...THREE.Object3D.prototype,
    ...THREE.EventDispatcher.prototype
};