<template>
  <button @click="showModal">Export to HTML</button>
</template>

<script>
export default {
  name: "ExportHTML",
  methods: {
    showModal: function () {
      this.$emit("modal", { display: true, mode: "export-html" });
    },
  },
};
</script>

<style scoped>
button {
  border-bottom: 1px solid #ffe8b3;
}
</style>
