// Helper to generate brush textures (simplified for Original, no matcaps)
// eslint-disable-next-line no-unused-vars
function generateBrushTexture(type, materialType) {
    if (type === 'solid') return null;
    const canvas = document.createElement('canvas');
    canvas.width = 64; canvas.height = 64;
    const ctx = canvas.getContext('2d');
    ctx.fillStyle = 'white';
    ctx.fillRect(0, 0, 64, 64);
    ctx.globalCompositeOperation = 'destination-in';
    if (type === 'soft') {
        const gradient = ctx.createLinearGradient(0, 0, 0, 64);
        gradient.addColorStop(0, 'rgba(255, 255, 255, 0)');
        gradient.addColorStop(0.5, 'rgba(255, 255, 255, 1)');
        gradient.addColorStop(1, 'rgba(255, 255, 255, 0)');
        ctx.fillStyle = gradient;
        ctx.fillRect(0, 0, 64, 64);
    } else if (type === 'noise') {
        const imgData = ctx.createImageData(64, 64);
        for (let i = 0; i < imgData.data.length; i += 4) {
            imgData.data[i] = 0; imgData.data[i + 1] = 0; imgData.data[i + 2] = 0;
            imgData.data[i + 3] = Math.random() * 255;
        }
        const tempC = document.createElement('canvas');
        tempC.width = 64; tempC.height = 64;
        tempC.getContext('2d').putImageData(imgData, 0, 0);
        ctx.drawImage(tempC, 0, 0);
    } else {
        ctx.fillStyle = 'white';
        ctx.fillRect(0, 0, 64, 64);
    }
    const texture = new THREE.CanvasTexture(canvas);
    texture.wrapS = THREE.RepeatWrapping;
    texture.wrapT = THREE.RepeatWrapping;
    return texture;
}

// Helper function to get the active layer group
function getActiveLayerGroup() {
    if (vm && vm.getActiveLayerGroup) {
        return vm.getActiveLayerGroup() || scene;
    }
    return scene;
}

let draw = {
    l: undefined,
    draw: class {
        constructor(stroke, fill) {
            // Defaults to ensure visibility if stroke object is empty
            this.stroke = {
                ...stroke,
                show_stroke: stroke.show_stroke !== false, // Default to true if undefined
                lineWidth: stroke.lineWidth || 0.1,        // Default to visible width
                color: stroke.color || '#000000',          // Default to black
                opacity: stroke.opacity !== undefined ? stroke.opacity : 1,
            };
            this.line = new MeshLine();
            this.geometry = new THREE.BufferGeometry();
            this.vertices = new Float32Array([]);
            this.geometry.setAttribute("position", new THREE.BufferAttribute(this.vertices, 3));

            this.material = new MeshLineMaterial({
                lineWidth: this.stroke.show_stroke ? this.stroke.lineWidth : 0.005,
                sizeAttenuation: 1,
                color: this.stroke.show_stroke ? this.stroke.color : 0xFFFFFF,
                side: THREE.DoubleSide,
                fog: true,
                wireframe: false,
                alphaTest: this.stroke.opacity < 1 ? 0 : 0.5,
                blending: THREE.NormalBlending,
                transparent: true,
                depthWrite: this.stroke.opacity >= 1,
                depthTest: true,
                repeat: new THREE.Vector2(this.stroke.textureScale || 1, 1),
                opacity: this.stroke.opacity !== undefined ? this.stroke.opacity : 1,
                resolution: new THREE.Vector2(window.innerWidth, window.innerHeight),
                dashArray: this.stroke.dashArray || 0,
                dashOffset: this.stroke.dashOffset || 0,
                dashRatio: this.stroke.dashRatio || 0.5,
            });

            // If soft brush or noise, we need specific material settings
            if (this.stroke.brushType === 'soft' || this.stroke.brushType === 'noise') {
                this.material.alphaTest = 0;
                this.material.depthWrite = false;
            }
            this.mesh = new THREE.Mesh(this.line, this.material);
            this.uuid = this.mesh.uuid;

            this.mesh.raycast = MeshLineRaycast;
            this.mesh.layers.set(1);
            this.bufferPoints = new Array();
            this.size = stroke.smoothing || 4;

            this.mesh.userData.vertices = new Array();
            this.mesh.userData.stroke = {
                show_stroke: stroke.show_stroke,
                color: stroke.color,
                lineWidth: stroke.lineWidth,
                opacity: stroke.opacity !== undefined ? stroke.opacity : 1,
                brushType: stroke.brushType,
                taper: stroke.taper !== undefined ? stroke.taper : 3,
                smoothing: stroke.smoothing || 4,
                textureScale: stroke.textureScale || 1,
                wiggle: stroke.wiggle || 0,
                animationSpeed: stroke.animationSpeed || 0,
                dashArray: stroke.dashArray || 0,
                dashOffset: stroke.dashOffset || 0,
                dashRatio: stroke.dashRatio || 0.5,
            };
            this.mesh.userData.stroke.force = new Array();
            this.mesh.userData.fill = { show_fill: fill.show_fill, color: fill.color };

            let position = new THREE.Vector3();
            canvas.getWorldPosition(position);
            let quaternion = new THREE.Quaternion();
            canvas.getWorldQuaternion(quaternion);
            let scale = new THREE.Vector3();
            canvas.getWorldScale(scale);

            this.mesh.userData.canvas = { shape: currentShape, position: position, quaternion: quaternion, scale: scale };

            this.fill = fill;
            this.fillGeometry = new THREE.BufferGeometry();
            this.fillVertices = new Float32Array([]);
            this.fillGeometry.setAttribute("position", new THREE.BufferAttribute(this.fillVertices, 3));
            this.uvsX = [];
            this.uvsY = [];
            this.uvs = [];
            this.triangles = new THREE.BufferAttribute(new Uint16Array(Earcut.triangulate(this.fillGeometry.attributes.position.array, null, 3)), 1);
            this.face = null;
            this.fillGeometry.setIndex(this.triangles);
            this.fillMaterial = new THREE.MeshBasicMaterial({
                color: this.fill.color,
                side: THREE.DoubleSide,
                wireframe: false,
                polygonOffset: this.stroke.show_stroke ? true : false,
                polygonOffsetFactor: this.stroke.lineWidth * 1000,
                depthTest: true,
                polygonOffsetUnits: -1,
                transparent: this.fill.show_fill ? true : false,
                opacity: this.fill.show_fill ? 0.05 : 0,
                fog: true,
                // map: texture
            });
            this.fillMesh = new THREE.Mesh(this.fillGeometry, this.fillMaterial);
            this.fillMesh.layers.set(1);
        }
        start(mirrorOn, radial, sectors, radialAxis, localSymmetry) {
            this.radial = radial;
            this.sectors = sectors;
            this.radialAxis = radialAxis || 'y';
            this.localSymmetry = localSymmetry;

            if (this.stroke.show_stroke === false && this.fill.show_stroke === false) return

            drawingScene.add(this.mesh);

            if (this.fill.show_fill) {
                this.mesh.add(this.fillMesh)
            }

            if (mirrorOn && typeof mirrorOn === 'string') {
                const axes = mirrorOn.split('');
                let currentObjects = [this.mesh];
                axes.forEach(axis => {
                    let nextObjects = [];
                    currentObjects.forEach(obj => {
                        let clone = mirror.object(obj, axis, drawingScene, this.localSymmetry ? canvas : null);
                        if (clone) nextObjects.push(clone);
                    });
                    currentObjects = currentObjects.concat(nextObjects);
                });
            }
            // console.log(x, y, z, unproject, force)

        }
        move(x, y, z, force, unproject) {

            if (this.stroke.show_stroke === false && this.fill.show_fill === false) return
            this.addVertex(x, y, z, force, unproject)
        }
        end_internal(mirrorOn, render) {
            const layerGroup = getActiveLayerGroup();
            layerGroup.add(this.mesh);
            drawingScene.clear();

            this.geometry.verticesNeedsUpdate = true;
            this.stroke.show_stroke ? '' : this.mesh.material.opacity = 0;
            this.fill.show_fill ? this.fillMesh.material.transparent = false : '';
            this.fill.show_fill ? this.fillMesh.material.opacity = 1 : '';

            this.mesh.position.set(
                this.mesh.geometry.boundingSphere.center.x,
                this.mesh.geometry.boundingSphere.center.y,
                this.mesh.geometry.boundingSphere.center.z
            );

            this.geometry.center();
            this.setGeometry("tail");
            this.geometry.needsUpdate = true;

            if (mirrorOn && typeof mirrorOn === 'string') {
                const axes = mirrorOn.split('');
                let currentObjects = [this.mesh];
                axes.forEach(axis => {
                    let nextObjects = [];
                    currentObjects.forEach(obj => {
                        let clone = mirror.object(obj, axis, layerGroup, this.localSymmetry ? canvas : null);
                        if (clone) nextObjects.push(clone);
                    });
                    currentObjects = currentObjects.concat(nextObjects);
                });
            }

            // Radial Symmetry Logic
            if (this.radial && this.sectors > 1) {
                const angleStep = (Math.PI * 2) / this.sectors;
                const m = this.localSymmetry ? canvas.matrixWorld : null;
                const mInv = m ? m.clone().invert() : null;

                const createRadialCopies = (targetMesh) => {
                    for (let i = 1; i < this.sectors; i++) {
                        const angle = angleStep * i;
                        const clone = targetMesh.clone();
                        clone.layers.set(1);
                        clone.raycast = MeshLineRaycast;

                        let rotMatrix = new THREE.Matrix4();
                        if (this.radialAxis === 'x') {
                            rotMatrix.makeRotationX(angle);
                        } else if (this.radialAxis === 'z') {
                            rotMatrix.makeRotationZ(angle);
                        } else {
                            rotMatrix.makeRotationY(angle);
                        }

                        if (this.localSymmetry && m && mInv) {
                            const transform = m.clone().multiply(rotMatrix).multiply(mInv);
                            clone.applyMatrix4(transform);
                        } else {
                            clone.position.applyMatrix4(rotMatrix);
                            if (this.radialAxis === 'x') clone.rotateX(angle);
                            else if (this.radialAxis === 'z') clone.rotateZ(angle);
                            else clone.rotateY(angle);
                        }

                        layerGroup.add(clone);
                    }
                };

                createRadialCopies(this.mesh);

                // For multi-axis mirroring, we should rotate all generated mirror copies
                layerGroup.children.forEach(child => {
                    if (child.userData && (child.userData.mirror || child.userData.mirrors || child.userData.mirrorParent) && child.parent === layerGroup) {
                        createRadialCopies(child);
                    }
                });
            }

            let vert = this.geometry.attributes.position.array;
            this.fillGeometry.setAttribute('position', new THREE.BufferAttribute(vert, 3));
            let triangles;

            var planeVector = (new THREE.Vector3(0, 0, 1)).applyQuaternion(this.fillMesh.quaternion);
            var cameraVector = (new THREE.Vector3(0, 0, -1)).applyQuaternion(camera.quaternion);

            if (planeVector.angleTo(cameraVector) > Math.PI / 2) { triangles = new THREE.BufferAttribute(new Uint16Array(Earcut.triangulate(this.fillGeometry.attributes.position.array, null, 3)), 1) } else { triangles = new THREE.BufferAttribute(new Uint16Array(Earcut.triangulate(this.fillGeometry.attributes.position.array, null, 3).reverse()), 1) }
            this.fillGeometry.setIndex(triangles);
            this.fillGeometry.computeBoundingSphere();


            if (render === true) {
                renderer.autoClear = true;
                renderer.render(scene, camera);
            }
        }
        end(mirrorOn) {

            if (this.stroke.show_stroke === false && this.fill.show_stroke === false) return
            if (this.geometry.attributes.position.array.length < 3) return

            this.end_internal(mirrorOn, true);

            let uuid = this.uuid;
            let vertices = this.geometry.attributes.position.array;
            let stroke = this.mesh.userData.stroke;
            let fill = this.mesh.userData.fill;
            let position = new THREE.Vector3();
            this.mesh.getWorldPosition(position);
            let quaternion = new THREE.Quaternion();
            this.mesh.getWorldQuaternion(quaternion);
            let scale = new THREE.Vector3();
            this.mesh.getWorldScale(scale);

            let matrix = this.mesh.matrix;

            undoManager.add({
                undo: function () {
                    erase.deleteObject(scene.getObjectByProperty(
                        "uuid",
                        uuid
                    ))
                    renderer.render(scene, camera);
                },
                redo: function () {
                    draw.fromVertices(
                        vertices,
                        stroke,
                        fill,
                        mirrorOn,
                        uuid,
                        //true
                        position,
                        quaternion,
                        scale,
                        matrix,
                        true
                    );
                }
            });

            undoRedoComponent.$.ctx.updateUi();

        }
        addVertex(x, y, z, force, unproject) {

            var v3 = new THREE.Vector3(x, y, z);
            if (unproject) {
                v3.unproject(camera);
            }
            var v4 = new THREE.Vector4(v3.x, v3.y, v3.z, force);
            let raycaster = new THREE.Raycaster();
            raycaster.setFromCamera(new THREE.Vector2(x, y), camera);
            try {
                var intersectedObject = raycaster.intersectObjects([canvas])[0];
                var nt = intersectedObject.point;
                if (this.face === null) {
                    if (
                        (Math.sign(intersectedObject.face.normal.y) === -1 || Object.is(Math.sign(intersectedObject.face.normal.y), -0))
                        ||
                        (Math.sign(intersectedObject.face.normal.z) === -1 || Object.is(Math.sign(intersectedObject.face.normal.z), -0))
                    ) {
                        this.face = 'back'
                    } else {
                        this.face = 'front'
                    }
                }
                let normal = { x: 0, y: 0, z: 0 };
                let wiggleVal = this.stroke.wiggle || 0;
                let wx = (Math.random() - 0.5) * wiggleVal;
                let wy = (Math.random() - 0.5) * wiggleVal;
                let wz = (Math.random() - 0.5) * wiggleVal;
                v4 = new THREE.Vector4(nt.x + normal.x + wx, nt.y + normal.y + wy, nt.z + normal.z + wz, force);
            } catch (err) {
                //expected if the line leaves the 3d canvas
                // console.log(err);
                return
            }

            function map(n, start1, stop1, start2, stop2) {
                return (
                    ((n - start1) / (stop1 - start1)) * (stop2 - start2) + start2
                );
            }

            if (unproject) {
                this.appendToBuffer(v4);
                let pt = this.getAveragePoint(0);
                if (pt) {
                    //stroke
                    this.mesh.userData.vertices.push([pt.x, pt.y, pt.z]);
                    this.mesh.userData.stroke.force.push(pt.w);

                    this.geometry.attributes.position.array = this.Float32Concat(this.geometry.attributes.position.array, new Float32Array([pt.x, pt.y, pt.z]));

                    this.uvsX.push(pt.x)
                    this.uvsY.push(pt.y)

                    this.geometry.attributes.position.count = this.geometry.attributes.position.count + 3
                    this.geometry.attributes.position.needsUpdate = true;

                    if (this.geometry.attributes.position.count > 3 && this.fill != false) {
                        let vert = this.geometry.attributes.position.array;
                        this.fillGeometry.setAttribute('position', new THREE.BufferAttribute(vert, 3));
                        this.fillGeometry.attributes.position.needsUpdate = true;
                        this.fillGeometry.computeBoundingSphere();

                        const uvLowestX = Math.min(...this.uvsX);
                        const uvHighestX = Math.max(...this.uvsX);
                        const uvLowestY = Math.min(...this.uvsY);
                        const uvHighestY = Math.max(...this.uvsY);

                        this.uvs = []
                        for (let u = 0; u < this.uvsX.length; u++) {
                            let x = map(this.uvsX[u], uvLowestX, uvHighestX, 0.01, 0.99)
                            let y = map(this.uvsY[u], uvLowestY, uvHighestY, 0.01, 0.99)
                            this.uvs.push(x, y);
                        }

                        this.fillGeometry.setAttribute('uv', new THREE.Float32BufferAttribute(this.uvs, 2));
                        this.fillGeometry.attributes.uv.needsUpdate = true;

                        let triangles = new THREE.BufferAttribute(new Uint16Array(Earcut.triangulate(this.fillGeometry.attributes.position.array, null, 3)), 1);
                        this.fillGeometry.index = triangles;
                        this.fillGeometry.index.needsUpdate = true;

                        this.fillGeometry.needsUpdate = true;
                    }

                }
            } else {
                this.mesh.userData.stroke.force.push(force);
                this.geometry.attributes.position.array = this.Float32Concat(this.geometry.attributes.position.array, new Float32Array([v3.x, v3.y, v3.z]));
                this.geometry.attributes.position.count = this.geometry.attributes.position.count + 3
                this.geometry.attributes.position.needsUpdate = true;

            }
            this.setGeometry();
            renderer.autoClear = false;
            renderer.clearDepth();
            renderer.render(drawingScene, camera);
        }
        Float32Concat(first, second) {
            var firstLength = first.length,
                result = new Float32Array(firstLength + second.length);

            result.set(first);
            result.set(second, firstLength);

            return result;
        }
        appendToBuffer(pt) {
            this.bufferPoints.push(pt);
            while (this.bufferPoints.length > this.size) {
                this.bufferPoints.shift();
            }
        }
        getAveragePoint(offset) {
            var len = this.bufferPoints.length;
            if (len % 2 === 1 || len >= this.size) {
                var totalX = 0;
                var totalY = 0;
                var totalZ = 0;
                var totalW = 0;
                var pt, i;
                var count = 0;
                for (i = offset; i < len; i++) {
                    count++;
                    pt = this.bufferPoints[i];
                    totalX += pt.x;
                    totalY += pt.y;
                    totalZ += pt.z;
                    totalW += pt.w;
                }
                return {
                    x: totalX / count,
                    y: totalY / count,
                    z: totalZ / count,
                    w: totalW / count,
                };
            }
            return null;
        }
        setGeometry(end) {
            this.line.setPoints(
                this.geometry.attributes.position.array,
                (p) => {
                    let length = this.geometry.attributes.position.array.length / 3;
                    let force = this.mesh.userData.stroke.force;
                    function map(n, start1, stop1, start2, stop2) {
                        return (
                            ((n - start1) / (stop1 - start1)) * (stop2 - start2) + start2
                        );
                    }
                    let index = Math.round(p * (length - 1));
                    let minWidth = 0;
                    let baseWidth = 3;
                    let width = force[index] * 16;
                    let tailLength = this.stroke.taper !== undefined ? this.stroke.taper : 3;

                    //Beginning of the line
                    if (index < tailLength) {
                        return map(
                            index,
                            0, // Start from index 0
                            tailLength,
                            minWidth,
                            baseWidth + width
                        );
                    }
                    //End of the line
                    else if (index > length - tailLength && end == "tail") {
                        return map(
                            index,
                            length - tailLength,
                            length - 1,
                            baseWidth + width,
                            minWidth
                        );
                    }
                    //bulk of the line
                    else {
                        return baseWidth + width;
                    }
                }
            );
        }
        setUvs() {
            function map(n, start1, stop1, start2, stop2) {
                return (
                    ((n - start1) / (stop1 - start1)) * (stop2 - start2) + start2
                );
            }

            const uvLowestX = Math.min(...this.uvsX);
            const uvHighestX = Math.max(...this.uvsX);
            const uvLowestY = Math.min(...this.uvsY);
            const uvHighestY = Math.max(...this.uvsY);
            this.uvs = []
            for (let u = 0; u < this.uvsX.length; u++) {
                let x = map(this.uvsX[u], uvLowestX, uvHighestX, 0.01, 0.99)
                let y = map(this.uvsY[u], uvLowestY, uvHighestY, 0.01, 0.99)
                this.uvs.push(x, y);
            }

            this.fillGeometry.setAttribute('uv', new THREE.Float32BufferAttribute(this.uvs, 2));
            this.fillGeometry.attributes.uv.needsUpdate = true;
        }
        cancel() {
            drawingScene.remove(this.mesh);
            mirror.eraseMirrorOf(this.mesh);
            this.mesh.material.dispose();
            draw.l = undefined;
            draw.l = new draw.draw();
            drawingScene.clear();
            renderer.render(drawingScene, camera);
            renderer.autoClear = true;
            renderer.render(scene, camera);
        }
    },
    onStart: function (x, y, z, force, unproject, mirrorOn, stroke, fill, radial, sectors, radialAxis, localSymmetry) {
        //this draw acceppts two arguments: stroke and fill. stroke: { show_stroke: bool, color: 'red', lineWidth: 3 }, fill: { show_fill: bool, color: 'black' },
        //I should gate the possibility of drawing something without stroke or fill
        this.l = new this.draw(stroke, fill);
        this.l.start(mirrorOn, radial, sectors, radialAxis, localSymmetry);
    },
    onMove: function (x, y, z, force, unproject) {
        this.l.move(x, y, z, force, unproject);
    },
    onEnd: function (mirrorOn) {
        this.l.end(mirrorOn);
    },
    onEnd_internal: function (mirrorOn, render) {
        this.l.end_internal(mirrorOn, render);
    },
    onCancel: function () {
        this.l ? this.l.cancel() : null;
    },
    fromVertices(vertices, stroke, fill, mirrorOn, uuid, position, quaternion, scale, matrix, render) {

        let verticesArray = [];
        // Check if vertices is array of Vector3 or flat array
        if (vertices.length > 0 && vertices[0].isVector3) {
            vertices.forEach((v3) => {
                verticesArray.push(v3.x || 0, v3.y || 0, v3.z || 0);
            });
        } else {
            // Assume it is already flat
            verticesArray = Array.from(vertices);
        }

        this.l = new this.draw(stroke, fill);
        this.onStart(0, 0, 0, 0, false, mirrorOn, stroke, fill);

        const float32Vertices = new Float32Array(verticesArray);
        this.l.geometry.setAttribute('position', new THREE.BufferAttribute(float32Vertices, 3));
        this.l.geometry.attributes.position.count = float32Vertices.length / 3;
        this.l.geometry.attributes.position.needsUpdate = true;

        // Defaults for optional transforms
        if (!matrix) matrix = new THREE.Matrix4();
        if (!position) position = new THREE.Vector3(0, 0, 0);
        if (!quaternion) quaternion = new THREE.Quaternion();
        if (!scale) scale = new THREE.Vector3(1, 1, 1);

        for (let i = 0; i < float32Vertices.length / 3; i++) {
            let v3 = new THREE.Vector3().fromArray(float32Vertices, i * 3).applyMatrix4(matrix);
            this.l.uvsX.push(v3.x)
            this.l.uvsY.push(v3.y)
        }

        this.l.setUvs();

        // If 'force' is missing from stroke data (e.g. new shape), fill it
        if (!stroke.force || stroke.force.length === 0) {
            this.l.mesh.userData.stroke.force = new Array(Math.floor(float32Vertices.length / 3)).fill(0.5); // Default pressure
        } else {
            this.l.mesh.userData.stroke.force = stroke.force;
        }
        this.l.setGeometry();

        if (uuid) { this.l.mesh.uuid = uuid; }
        this.onEnd_internal(mirrorOn, false);
        if (matrix) {
            this.l.mesh.applyMatrix4(matrix)
        }
        if (position) {
            this.l.mesh.position.set(
                position.x,
                position.y,
                position.z
            );
        }
        if (quaternion) {
            this.l.mesh.quaternion.set(
                quaternion._x,
                quaternion._y,
                quaternion._z,
                quaternion._w
            );
        }
        if (scale) {
            this.l.mesh.scale.set(
                scale.x,
                scale.y,
                scale.z
            );
        }
        // Direct scene add for simplicity in restore or fetch active layer if needed
        let lg = getActiveLayerGroup();
        lg.add(this.l.mesh);

        if (render === true) { renderer.render(scene, camera) }
    },
    updateStroke: function (mesh, stroke) {
        if (!mesh || !mesh.userData.stroke) return;

        // Simplified update for Original
        // Update UserData
        Object.assign(mesh.userData.stroke, stroke);

        // Update Material
        const mat = mesh.material;
        if (stroke.color) mat.color.setStyle(stroke.color);
        if (stroke.lineWidth !== undefined) {
            mat.lineWidth = stroke.lineWidth;
            if (mat.uniforms.lineWidth) mat.uniforms.lineWidth.value = stroke.lineWidth;
        }

        if (stroke.opacity !== undefined) {
            mat.opacity = stroke.opacity;
            mat.transparent = true;
            if (mat.uniforms.opacity) mat.uniforms.opacity.value = stroke.opacity;
        }

        if (stroke.dashArray !== undefined) {
            mat.dashArray = stroke.dashArray;
            if (mat.uniforms.dashArray) mat.uniforms.dashArray.value = stroke.dashArray;
        }
        if (stroke.dashOffset !== undefined) {
            mat.dashOffset = stroke.dashOffset;
            if (mat.uniforms.dashOffset) mat.uniforms.dashOffset.value = stroke.dashOffset;
        }
        if (stroke.dashRatio !== undefined) {
            mat.dashRatio = stroke.dashRatio;
            if (mat.uniforms.dashRatio) mat.uniforms.dashRatio.value = stroke.dashRatio;
        }

        if (stroke.textureScale !== undefined) {
            mat.repeat.x = stroke.textureScale;
            if (mat.uniforms.repeat) mat.uniforms.repeat.value.x = stroke.textureScale;
        }

        // No matcap support in Original
        // update texture if stroke.brushType changes (basic types)

        const brushTexture = generateBrushTexture(
            stroke.brushType || mesh.userData.stroke.brushType,
            'standard'
        );

        if (mat.map && mat.map !== brushTexture) mat.map.dispose();

        if (brushTexture) {
            mat.map = brushTexture;
            mat.alphaMap = brushTexture;
            mat.useMap = 1;
            mat.transparent = true;

            if (stroke.brushType === 'soft' || stroke.brushType === 'noise') {
                mat.alphaTest = 0;
                mat.depthWrite = false;
            } else {
                mat.alphaTest = 0.5;
                mat.depthWrite = true;
            }

            if (mat.uniforms.map) mat.uniforms.map.value = brushTexture;
            if (mat.uniforms.alphaMap) mat.uniforms.alphaMap.value = brushTexture;

            // Re-apply opacity logic just in case
            if (mat.opacity < 1) {
                mat.alphaTest = 0;
                mat.depthWrite = false;
            }
        }
    },
    updateFill: function (mesh, fill) {
        if (!mesh || !mesh.userData.fill) return;
        Object.assign(mesh.userData.fill, fill);
        // ... fill update logic if needed
    },
    animateLines: function (delta) {
        let animated = false;
        if (!scene) return false;

        const updateMesh = (mesh) => {
            if (mesh.userData && mesh.userData.stroke && mesh.userData.stroke.animationSpeed > 0) {
                if (mesh.material.uniforms && mesh.material.uniforms.dashOffset) {
                    mesh.material.uniforms.dashOffset.value -= mesh.userData.stroke.animationSpeed * delta;
                    animated = true;
                }
            }
        };

        scene.traverse((child) => {
            if (child.isMesh && child.userData.stroke) {
                updateMesh(child);
            }
        });

        return animated;
    }
}


export { draw }