<template>
  <div
    class="draggable-window"
    :class="{ minimized: minimized }"
    :style="{ top: currentY + 'px', left: currentX + 'px' }"
    v-show="visible && !userClosed"
  >
    <div class="window-header" @mousedown="startDrag" @touchstart="startDragTouch">
      <div class="drag-handle">
        <div class="handle-dots">
          <span></span><span></span><span></span>
        </div>
        <span class="title">{{ title }}</span>
      </div>
      <div class="controls">
        <button 
          class="control-btn minimize" 
          @click.stop="toggleMinimize" 
          @mousedown.stop 
          @touchstart.stop
        >
          {{ minimized ? '+' : '' }}
        </button>
        <button 
          class="control-btn close" 
          @click.stop="close" 
          @mousedown.stop 
          @touchstart.stop
        >
          
        </button>
      </div>
    </div>
    <div class="window-content" v-show="!minimized" v-bind="$attrs">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "DraggableWindow",
  inheritAttrs: false,
  props: {
    title: {
      type: String,
      default: "Window",
    },
    initialX: {
      type: Number,
      default: 100,
    },
    initialY: {
      type: Number,
      default: 100,
    },
    visible: {
      type: Boolean,
      default: true,
    },
    reopenTrigger: {
      type: Number,
      default: 0,
    },
    startOpen: {
      type: Boolean,
      default: true,
    },
    isOpen: {
      type: Boolean,
      default: undefined,
    },
  },
  data() {
    return {
      isDragging: false,
      currentX: this.initialX,
      currentY: this.initialY,
      dragOffsetX: 0,
      dragOffsetY: 0,
      minimized: false,
      userClosed: this.isOpen !== undefined ? !this.isOpen : !this.startOpen,
    };
  },
  methods: {
    startDrag(event) {
      if (event.target.closest('.controls')) return;
      this.isDragging = true;
      this.dragOffsetX = event.clientX - this.currentX;
      this.dragOffsetY = event.clientY - this.currentY;
      document.addEventListener("mousemove", this.onDrag);
      document.addEventListener("mouseup", this.stopDrag);
    },
    onDrag(event) {
      if (this.isDragging) {
        this.currentX = event.clientX - this.dragOffsetX;
        this.currentY = event.clientY - this.dragOffsetY;
      }
    },
    stopDrag() {
      this.isDragging = false;
      document.removeEventListener("mousemove", this.onDrag);
      document.removeEventListener("mouseup", this.stopDrag);
    },
    // Touch support
    startDragTouch(event) {
      if (event.target.closest('.controls')) return;
      const touch = event.touches[0];
      this.isDragging = true;
      this.dragOffsetX = touch.clientX - this.currentX;
      this.dragOffsetY = touch.clientY - this.currentY;
      document.addEventListener("touchmove", this.onDragTouch);
      document.addEventListener("touchend", this.stopDragTouch);
    },
    onDragTouch(event) {
       if (this.isDragging) {
         event.preventDefault(); // Prevent scrolling while dragging
         const touch = event.touches[0];
         this.currentX = touch.clientX - this.dragOffsetX;
         this.currentY = touch.clientY - this.dragOffsetY;
       }
    },
    stopDragTouch() {
       this.isDragging = false;
       document.removeEventListener("touchmove", this.onDragTouch);
       document.removeEventListener("touchend", this.stopDragTouch);
    },
    toggleMinimize() {
      this.minimized = !this.minimized;
    },
    close() {
      this.userClosed = true;
      this.$emit("close");
    },
  },
  watch: {
    reopenTrigger() {
      this.userClosed = false;
    },
    isOpen(val) {
      if (val !== undefined) {
         this.userClosed = !val;
      }
    },
    initialX(val) {
      this.currentX = val;
    },
    initialY(val) {
      this.currentY = val;
    },
  },
};
</script>

<style scoped>
.draggable-window {
  position: absolute;
  z-index: 1000;
  background: rgba(20, 20, 25, 0.65);
  backdrop-filter: blur(24px) saturate(180%);
  -webkit-backdrop-filter: blur(24px) saturate(180%);
  border-radius: 20px;
  box-shadow: 
    0 4px 6px -1px rgba(0, 0, 0, 0.1), 
    0 2px 4px -1px rgba(0, 0, 0, 0.06),
    0 24px 60px -12px rgba(0, 0, 0, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.08);
  display: flex;
  flex-direction: column;
  min-width: 180px;
  transition: transform 0.2s cubic-bezier(0.34, 1.56, 0.64, 1), opacity 0.2s ease, width 0.2s ease, height 0.2s ease;
  overflow: visible;
  user-select: none;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  color: #f0f0f0;
  pointer-events: auto;
}

.minimized {
  height: auto !important;
  width: auto !important;
  min-width: auto;
  border-radius: 14px;
  background: rgba(30, 30, 35, 0.85);
  border: 1px solid rgba(255, 255, 255, 0.15);
}

.window-header {
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 14px;
  cursor: grab;
  background: linear-gradient(to bottom, rgba(255,255,255,0.06), transparent);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.window-header:active {
  cursor: grabbing;
}

.drag-handle {
  display: flex;
  align-items: center;
  flex-grow: 1;
}

.handle-dots {
  display: flex;
  margin-right: 12px;
  gap: 4px;
}

.handle-dots span {
  width: 5px;
  height: 5px;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  transition: background-color 0.2s;
}

.draggable-window:hover .handle-dots span {
  background-color: rgba(255, 255, 255, 0.3);
}

.title {
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 0.3px;
  color: rgba(255, 255, 255, 0.9);
  pointer-events: none;
  text-shadow: 0 1px 2px rgba(0,0,0,0.5);
}

.controls {
  display: flex;
  gap: 8px;
}

.control-btn {
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  cursor: pointer;
  padding: 0;
  line-height: 1;
  background-color: rgba(255, 255, 255, 0.08);
  color: rgba(255, 255, 255, 0.6);
  transition: all 0.2s cubic-bezier(0.25, 0.8, 0.25, 1);
  box-shadow: inset 0 0 0 1px rgba(255,255,255,0.05);
}

.control-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  transform: scale(1.05);
}

.control-btn:active {
  transform: scale(0.95);
}

.control-btn.close:hover {
  background-color: rgba(255, 95, 87, 0.8);
  color: white;
}

.window-content {
  padding: 18px;
  display: flex;
  flex-direction: column;
}
</style>
