<template>
  <div>
    <div class="popup" v-if="show">
      <ToggleWindow label="Tools" @click="toggleWindow('tools')" :checked="windowStates && windowStates.tools" />
      <ToggleWindow label="Properties" @click="toggleWindow('properties')" :checked="windowStates && windowStates.properties" />
      <ToggleWindow label="Canvas" @click="toggleWindow('canvas')" :checked="windowStates && windowStates.canvas" />
      <ToggleWindow label="Shapes" @click="toggleWindow('shapes')" :checked="windowStates && windowStates.shapes" />
      <ToggleWindow label="Object Manager" @click="toggleWindow('objectManager')" :checked="windowStates && windowStates.objectManager" />
      <ToggleWindow label="History" @click="toggleWindow('history')" :checked="windowStates && windowStates.history" />
      <ToggleWindow label="View" @click="toggleWindow('view')" :checked="windowStates && windowStates.view" />
      <ToggleWindow label="Colors" @click="toggleWindow('colors')" :checked="windowStates && windowStates.colors" />
      <ToggleWindow label="Scene" @click="toggleWindow('scene')" :checked="windowStates && windowStates.scene" />
      <ToggleWindow label="Symmetry" @click="toggleWindow('symmetry')" :checked="windowStates && windowStates.symmetry" />
      <ToggleWindow label="Layers" @click="toggleWindow('layers')" :checked="windowStates && windowStates.layers" />
      <div class="separator"></div>
      <Save />
      <Load ref="load" />
      <div class="separator"></div>
      <export-for-blender @modal="showModal" />
      <export-for-procreate @modal="showModal" />
      <ExportUSDZ v-if="isSafari" />
      <ExportGLB />
      <ExportHTML @modal="showModal" />
      <start-preview @preview="startPreview" />
    </div>
    <button
      class="toggle-menu"
      @click="show = !show"
      v-bind:style="[show === true ? 'z-index: 10' : '']"
    >
      <img src="@/assets/icons/share.svg" alt="Share your sketch" />
    </button>
    <div class="fade" v-if="show" @click="show = false"></div>
  </div>
</template>

<script>
import ToggleWindow from "./MenuButtons/ToggleWindow.vue";
import Save from "./MenuButtons/Save.vue";
import Load from "./MenuButtons/Load.vue";
import ExportForBlender from "./MenuButtons/ExportForBlender.vue";
import ExportForProcreate from "./MenuButtons/ExportForProcreate.vue";
import StartPreview from "./MenuButtons/StartPreview.vue";
import ExportUSDZ from "./MenuButtons/ExportUSDZ.vue";
import ExportGLB from "./MenuButtons/ExportGLB.vue";
import ExportHTML from "./MenuButtons/ExportHTML.vue";

export default {
  name: "Share",
  props: {
    windowStates: Object,
  },
  components: {
    ToggleWindow,
    Save,
    Load,
    ExportForBlender,
    ExportForProcreate,
    StartPreview,
    ExportUSDZ,
    ExportGLB,
    ExportHTML,
  },
  emits: ["modal-set", "preview", "toggle-window"],
  data() {
    return {
      show: false,
      isSafari: false,
    };
  },
  methods: {
    hide: function () {
      this.show = false;
    },
    showModal: function (val) {
      this.$emit("modal-set", val);
      this.hide();
    },
    startPreview: function (val) {
      this.$emit("preview", val);
      this.hide();
    },
    toggleWindow: function (windowName) {
      this.$emit("toggle-window", windowName);
    },
  },
  watch: {},
  mounted() {
    this.isSafari =
      navigator.vendor &&
      navigator.vendor.indexOf("Apple") > -1 &&
      navigator.userAgent &&
      navigator.userAgent.indexOf("CriOS") == -1 &&
      navigator.userAgent.indexOf("FxiOS") == -1;
  },
};
</script>

