<template>
  <draggable-window 
    title="Symmetry" 
    :initialX="12" 
    :initialY="680"
    :visible="visible"
    :reopenTrigger="reopenTrigger"
    :is-open="isOpen"
    @close="$emit('close')"
  >
    <div class="symmetry-controls">
      <div class="symmetry-options">
        <button 
          class="symmetry-btn"
          :class="{ active: isAxisActive('x') }"
          @click="toggleAxis('x')"
        >
          <span>X</span>
        </button>
        <button 
          class="symmetry-btn"
          :class="{ active: isAxisActive('y') }"
          @click="toggleAxis('y')"
        >
          <span>Y</span>
        </button>
        <button 
          class="symmetry-btn"
          :class="{ active: isAxisActive('z') }"
          @click="toggleAxis('z')"
        >
          <span>Z</span>
        </button>
      </div>
      
      <div class="symmetry-row" v-if="(typeof mirror === 'string' && mirror.length > 0) || radial">
        <label class="toggle-control">
            <input type="checkbox" :checked="localSymmetry" @change="toggleLocal">
            Local Symmetry
            <span class="info-icon" title="Symmetry relative to the Canvas instead of World Origin">?</span>
        </label>
      </div>
      
      <div class="radial-section">
        <label class="radial-toggle">
            <input type="checkbox" :checked="radial" @change="toggleRadial">
            Radial
        </label>
        
        <div class="symmetry-options" v-if="radial">
            <button 
              class="symmetry-btn small"
              :class="{ active: radialAxis === 'x' }"
              @click="setRadialAxis('x')"
            >
              <span>X</span>
            </button>
            <button 
              class="symmetry-btn small"
              :class="{ active: radialAxis === 'y' }"
              @click="setRadialAxis('y')"
            >
              <span>Y</span>
            </button>
            <button 
              class="symmetry-btn small"
              :class="{ active: radialAxis === 'z' }"
              @click="setRadialAxis('z')"
            >
              <span>Z</span>
            </button>
        </div>

        <div class="sectors-control" v-if="radial">
            <span>{{ sectors }}</span>
            <input 
                type="range" 
                min="2" 
                max="36" 
                step="1" 
                :value="sectors" 
                @input="updateSectors"
            >
        </div>
      </div>
    </div>
  </draggable-window>
</template>

<script>
import DraggableWindow from "./DraggableWindow.vue";

export default {
  name: "SymmetrySettings",
  components: { DraggableWindow },
  props: {
    visible: Boolean,
    reopenTrigger: Number,
    isOpen: Boolean,
    mirror: [Boolean, String],
    radial: Boolean,
    sectors: Number,
    radialAxis: {
        type: String,
        default: 'y'
    },
    localSymmetry: Boolean
  },
  emits: ["set-mirror", "set-radial", "set-sectors", "set-radial-axis", "set-local-symmetry", "close"],
  methods: {
    toggleAxis(axis) {
      let current = typeof this.mirror === 'string' ? this.mirror : "";
      if (current.includes(axis)) {
          current = current.replace(axis, "");
      } else {
          current += axis;
      }
      this.$emit("set-mirror", current);
    },
    isAxisActive(axis) {
      return typeof this.mirror === 'string' && this.mirror.includes(axis);
    },
    toggleLocal(e) {
      this.$emit("set-local-symmetry", e.target.checked);
    },
    toggleRadial(e) {
      this.$emit("set-radial", e.target.checked);
    },
    updateSectors(e) {
      this.$emit("set-sectors", parseInt(e.target.value));
    },
    setRadialAxis(val) {
      this.$emit("set-radial-axis", val);
    }
  },
};
</script>

<style scoped>
.symmetry-controls {
  padding: 8px;
  background: rgba(30, 30, 30, 0.8);
  border-radius: 8px;
}

.symmetry-options {
  display: flex;
  gap: 8px;
}

.symmetry-btn {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.symmetry-btn:hover {
  background: rgba(255, 255, 255, 0.15);
}

.symmetry-btn.active {
  background: var(--accent-color, #ffb000);
  border-color: var(--accent-color, #ffb000);
  color: black;
}

.radial-section {
    margin-top: 12px;
    border-top: 1px solid rgba(255,255,255,0.1);
    padding-top: 12px;
    color: white;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.radial-toggle, .toggle-control {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
}

.symmetry-row {
    margin-top: 12px;
    padding-top: 8px;
    border-top: 1px solid rgba(255,255,255,0.05);
    color: white;
    font-size: 11px;
}

.info-icon {
    width: 14px;
    height: 14px;
    background: rgba(255,255,255,0.2);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 9px;
    margin-left: 4px;
}

.sectors-control {
    display: flex;
    align-items: center;
    gap: 8px;
}

.sectors-control input {
    flex: 1;
}

.symmetry-btn.small {
    width: 30px;
    height: 30px;
    font-size: 10px;
}
</style>
