<template>
  <draggable-window
    title="2D Shapes"
    :initialX="100"
    :initialY="140"
    :visible="visible"
    :reopenTrigger="reopenTrigger"
    :is-open="isOpen"
    @close="$emit('close')"
  >
    <div class="shapes-grid">
      <button class="shape-btn" @click.stop="createShape('line')" @mousedown.stop @mouseup.stop title="Line">
         <span class="icon">/</span>
      </button>
      <button class="shape-btn" @click.stop="createShape('triangle')" @mousedown.stop @mouseup.stop title="Triangle">
         <span class="icon"></span>
      </button>
       <button class="shape-btn" @click.stop="createShape('square')" @mousedown.stop @mouseup.stop title="Square">
         <span class="icon"></span>
      </button>
      <button class="shape-btn" @click.stop="createShape('circle')" @mousedown.stop @mouseup.stop title="Circle">
         <span class="icon"></span>
      </button>
      <button class="shape-btn" @click.stop="createShape('ellipse')" @mousedown.stop @mouseup.stop title="Ellipse">
         <span class="icon"></span>
      </button>
      <button class="shape-btn" @click.stop="createShape('hexagon')" @mousedown.stop @mouseup.stop title="Hexagon">
         <span class="icon"></span>
      </button>
    </div>
  </draggable-window>
</template>

<script>
import DraggableWindow from "./DraggableWindow.vue";
import { canvas } from "./Canvas.vue";
import * as THREE from "three";
import { camera } from "../App.vue";

export default {
  name: "TwoDShapes",
  components: { DraggableWindow },
  props: {
    visible: Boolean,
    reopenTrigger: Number,
    isOpen: Boolean,
  },
  emits: ["close", "add-shape"],
  methods: {
    getCenterPoint() {
        const raycaster = new THREE.Raycaster();
        raycaster.setFromCamera(new THREE.Vector2(0, 0), camera);
        
        let target = canvas;
        // If canvas is not directly available (e.g. not exported properly or not mounted), fallback
        if (!target) {
             const vec = new THREE.Vector3(0, 0, -10);
             vec.applyQuaternion(camera.quaternion);
             vec.add(camera.position);
             return { point: vec, normal: new THREE.Vector3(0, 0, 1) };
        }

        const intersects = raycaster.intersectObject(target, true); // true for recursive if canvas is group
        
        if (intersects.length > 0) {
            return { point: intersects[0].point, normal: intersects[0].face.normal };
        }
        
        // Fallback if no hit
        const vec = new THREE.Vector3(0, 0, -10);
        vec.applyQuaternion(camera.quaternion);
        vec.add(camera.position);
        return { point: vec, normal: new THREE.Vector3(0, 0, 1) };
    },
    createShape(type) {
      const { point: center, normal } = this.getCenterPoint();
      const size = 1.0; 
      
      const shape = new THREE.Shape();
      
      switch (type) {
        case 'line': {
            // Create a thin rectangle for the line
            const thickness = 0.05;
            shape.moveTo(-size, -thickness);
            shape.lineTo(size, -thickness);
            shape.lineTo(size, thickness);
            shape.lineTo(-size, thickness);
            shape.lineTo(-size, -thickness);
            break;
        }
        case 'triangle':
            shape.moveTo(size * Math.cos(-Math.PI/2), size * Math.sin(-Math.PI/2));
            shape.lineTo(size * Math.cos(Math.PI/6), size * Math.sin(Math.PI/6));
            shape.lineTo(size * Math.cos(5*Math.PI/6), size * Math.sin(5*Math.PI/6));
            shape.closePath();
            break;
        case 'square':
             shape.moveTo(-size, -size);
             shape.lineTo(size, -size);
             shape.lineTo(size, size);
             shape.lineTo(-size, size);
             shape.closePath();
            break;
        case 'circle':
             shape.absarc(0, 0, size, 0, Math.PI * 2, false);
            break;
        case 'ellipse':
             shape.ellipse(0, 0, size * 1.5, size * 0.75, 0, Math.PI * 2, false, 0);
            break;
        case 'hexagon':
              for (let i = 0; i < 6; i++) {
                const angle = (i * 2 * Math.PI) / 6;
                const x = size * Math.cos(angle);
                const y = size * Math.sin(angle);
                if (i === 0) shape.moveTo(x, y);
                else shape.lineTo(x, y);
            }
            shape.closePath();
            break;
      }
      
      const geometry = new THREE.ShapeGeometry(shape);
      const mesh = new THREE.Mesh(geometry); // Material will be assigned by Canvas.vue
      
      // Orientation and Position
      mesh.quaternion.copy(camera.quaternion);
      
      // Apply offset to center
      let offset = normal.clone().multiplyScalar(0.05);
      let finalCenter = center.clone().add(offset);
      mesh.position.copy(finalCenter);
      
      mesh.name = type.charAt(0).toUpperCase() + type.slice(1);
      
      this.$emit('add-shape', mesh); // Delegate to App.vue
    },
  }
};
</script>

<style scoped>
.shapes-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;
  padding: 8px;
}

.shape-btn {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: white;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.shape-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.icon {
   line-height: 1;
}
</style>
